import * as types from '../../store/mutation-types'
// import createDragItem from '../drag_item/drag_item'
import brwsr from '../../services/browser'

export default {
    name: 'recent-item',
    props: [
        'model',
        'window'
    ],
    data () {
        return {
            opened: false
        }
    },
    computed: {
        isWindow () {
            return this.model.hasOwnProperty('window');
        },
        id () {
            return this.isWindow ? this.model.window.sessionId : this.model.tab.sessionId;
        },
        hiddenItems () {
            return this.$store.state.recentHiddenItems;
        },
        isHidden () {
            if(!this.isWindow && this.hiddenItems.indexOf(this.model.tab.sessionId + '_' + this.model.lastModified) > -1){
                return true;
            } else if(this.window && this.model.tab && this.hiddenItems.indexOf(this.window.sessionId + '_' + this.model.tab.sessionId) > -1){
                return true;
            } else if(this.model.tab && this.model.tab.windowSessionId && this.hiddenItems.indexOf(this.model.tab.windowSessionId + '_' + this.model.tab.sessionId) > -1){
                return true; // windowSessionId from device
            }
            return false;
        }
    },
    methods: {
        restore () {
            if(!this.isWindow){
                brwsr.restoreSession(this.id);
            } else {
                this.opened = !this.opened;
            }
        },
        restoreWindow () {
            brwsr.restoreSession(this.id);
        },
        dragStart (e) {
            // e.dataTransfer.setDragImage(createDragItem(this.model.tab.url, this.model.tab.title), -20, 0);
            this.$store.commit(types.SET_DRAG_ITEM, {
                type: this.isWindow ? types.DRAG_ITEM_TYPE_BROWSER_WINDOW : types.DRAG_ITEM_TYPE_RECENT,
                startEvent: e,
                data: this.isWindow ? this.model.window : this.model.tab
            });
        },
        hide () {
            let id = this.model.tab.sessionId + '_' + this.model.lastModified;
            if(this.window){
                id = this.window.sessionId + '_' + this.model.tab.sessionId;
            } else if(this.model.tab.windowSessionId){
                id = this.model.tab.windowSessionId + '_' + this.model.tab.sessionId;
            }
            if(this.hiddenItems.indexOf(id) === -1){
                this.$store.commit(types.ADD_TO_RECENT_HIDDEN_ITEMS, id);
            }
        }
    }
}