import * as types from '../../store/mutation-types'
// import createDragItem from '../drag_item/drag_item'
import brwsr from '../../services/browser'

export default {
	data () {
		return {
			items: []
		}
	},
	mounted () {
		this.getItems();
	},
	methods: {
		getItems () {
			brwsr.getTopSites().then((sites) => {
				this.items = sites;
			});
		},
		open (item) {
			window.location.replace(item.url);
		},
		dragStart (item, e) {
			// e.dataTransfer.setDragImage(createDragItem(item.url, item.title), -20, 0);
			this.$store.commit(types.SET_DRAG_ITEM, {
				type: types.DRAG_ITEM_TYPE_TOP_SITE,
				startEvent: e,
				data: item
			});
		}
	}
}