import * as types from "../../store/mutation-types";

export default {
    name: "background",
    props: [
        'decor',
        'background'
    ],
    data () {
        return {

        }
    },
    computed: {
        background () {
            return this.decor.background;
        },
        isPicture () {
            return !this.isColor && this.background.type === 'image';
        },
        isPattern () {
            return !this.isColor && this.background.type === 'pattern';
        },
        isColor () {
            return this.background.color.length;
        },
        isEffect () {
            return (this.isPattern || this.isPicture) && this.background.effect.blend;
        },
        isDarkTheme () {
            return this.decor.theme.name === types.DARK_THEME_NAME;
        },
        effectStyles () {
            return {
                'mix-blend-mode': this.background.effect.blend,
                'background-color': this.background.effect.color.length ? 'rgba(' + this.background.effect.color + ')' : '',
            }
        },
        opacityStyles () {
            return {
                'mix-blend-mode': this.isDarkTheme ? 'multiply' : 'lighten',
                'background-color': this.isDarkTheme ? 'rgba(0, 0, 0,' + this.background.opacity + ')' :
                    'rgba(255, 255, 255,' + this.background.opacity + ')',
            }
        },
        patternStyles () {
            return {
                'background': 'url("' + this.background.url + '")'
            }
        },
    }
}