import TreeItem from './bookmarks_tree_item.vue'
import * as types from '../../store/mutation-types'
import brwsr from '../../services/browser'

export default {
    components: {
        TreeItem
    },
    props: [
        // 'browser'
    ],
    data: function () {
        return {
            currentFolder: null,
            tmpTree: [],
            items: {
                id: brwsr.BOOKMARKS_ROOT_FOLDER_ID,
                title: 'My bookmarks',
                children: []
            }
        }
    },
    computed: {
        browser () {
            let browser = this.$store.state.browsers.find( b => b.name === this.activeBrowser );
            if(!browser || browser.quickStart){
                return this.$store.state.browsers[1];
            }
            // return !browser.quickStart ? browser : this.$store.state.browsers[1];
            return browser;
        },
        activeBrowser () {
            return this.$store.state.activeBrowser;
        }
    },
    created: function () {
        this.$store.commit(types.RESTORE_TREE_FOLDERS_STATE);
    },
    mounted: function () {
        this.getBookmarksTree();
        this.eventsHandler();
    },
    methods: {
        getBookmarksTree () {
            if(window.localStorage.hasOwnProperty(types.STORAGE_KEY_CACHED_TREE)){
                this.items = JSON.parse(window.localStorage[types.STORAGE_KEY_CACHED_TREE])[0];
            } else {
                brwsr.getBookmarksTree().then((bookmarks) => {
                    // this.items = [];
                    this.buildFoldersTree(bookmarks[0]);
                });
            }
        },
        buildFoldersTree (bookmark, parent) {
            if(brwsr.isBookmarkFolder(bookmark)){
                let newBookmark = {
                    title: bookmark.title,
                    id: bookmark.id,
                    index: bookmark.index,
                    parentId: bookmark.parentId,
                    dateAdded: bookmark.dateAdded,
                    dateGroupModified: bookmark.dateGroupModified,
                    children: []
                };
                if(!parent){
                    this.tmpTree.push(newBookmark);
                    this.items = this.tmpTree[0];
                } else {
                    parent.push(newBookmark);
                }
                for(let i=0; i < bookmark.children.length; i++){
                    this.buildFoldersTree(bookmark.children[i], newBookmark.children);
                }
            }
        },
        getIndex (parentId, folderId, callback) {
        	brwsr.getBookmarkChildren(parentId).then((children) => {
				let childFolders = [];
				for(let i=0; i < children.length; i++){
					if(brwsr.isBookmarkFolder(children[i])){
						childFolders.push(children[i]);
					}
				}
				for(let z=0; z < childFolders.length; z++){
					if(childFolders[z].id === folderId && typeof callback == 'function'){
						callback(z);
					}
				}
			});
        },
        getFolder (bookmark, id, parent, callback) {
            if(bookmark.id === id && typeof callback == 'function'){
                callback(bookmark, parent);
            }
            if(bookmark.hasOwnProperty('children')){
                for(let i=0; i < bookmark.children.length; i++){
                    this.getFolder(bookmark.children[i], id, bookmark.children, callback);
                }
            }
        },
        createFolder (parent, folder) {
            if(!folder.hasOwnProperty('children')){
                folder.children = [];
            }
            this.getIndex(parent.id, folder.id, (index) => {
                if(typeof parent.children == 'undefined'){
                    this.$set(parent, 'children', []);
                }
                parent.children.splice(index, 0, folder);
            });
        },
        eventsHandler () {
            this.$root.$on('bookmarkCreated', (bookmark) => {
                if(brwsr.isBookmarkFolder(bookmark)){
                    this.getFolder(this.items, bookmark.parentId, false,(parentFolder, parent) => {
                        this.createFolder(parentFolder, bookmark);
                    });
                }
            });
            this.$root.$on('bookmarkMoved', (id, info, bookmark) => {
                if(brwsr.isBookmarkFolder(bookmark)){
                    this.getFolder(this.items, id, false, (bm, parent) => {
                        if(info.parentId === info.oldParentId){
                            this.getIndex(bookmark.parentId, id, function (index) {
                                if(index !== parent.indexOf(bm)){
                                    parent.splice(parent.indexOf(bm), 1);
                                    parent.splice(index, 0, bm);
                                }
                            });
                        } else {
                            parent.splice(parent.indexOf(bm), 1);
                            this.getFolder(this.items, info.parentId, false, (parentFolder, parent) => {
                                this.createFolder(parentFolder, bm);
                            });
                        }
                    });
                }
            }); // TODO -_-
            this.$root.$on('bookmarkRemoved', (id, info) => {
                if(brwsr.isBookmarkFolder(info.node)){
                    this.getFolder(this.items, id, false, function (bm, parent) {
                        parent.splice(parent.indexOf(bm), 1);
                    });
                }
            });
            this.$root.$on('bookmarkChanged', (bookmark) => {
                if(brwsr.isBookmarkFolder(bookmark)){
                    this.getFolder(this.items, bookmark.id, false, function (bm) {
                        bm.title = bookmark.title;
                    });
                }
            });
        }
    }
}