import * as types from '../../store/mutation-types'
import MenuBarItem from './menu_bar_item.vue'

export default {
    props: [
        'items'
    ],
    components: {
        MenuBarItem
    },
    data () {
        return {
            backBtn: false,
            forwardBtn: false,
            itemsElm: {}
        }
    },
    mounted () {
        this.$set(this, 'itemsElm', this.$el.querySelector('.menu-bar-items'));
        this.checkBackForwardBtns();
        this.$watch('$store.state.columns', (newVal, oldVal) => {
            this.checkBackForwardBtns();
        }, {
            deep: true
        });
        window.addEventListener('resize', this.checkBackForwardBtns);
    },
    updated () {
        this.checkBackForwardBtns();
    },
    methods: {
        addBrowser () {
            this.$store.commit(types.ADD_NEW_BB, {});
        },
        checkBackForwardBtns () {
            this.$nextTick(()=>{
                if(this.itemsElm.scrollWidth < this.itemsElm.clientWidth && this.itemsElm.scrollLeft > 0){
                    this.itemsElm.scrollLeft = 0;
                }
                this.forwardBtn = this.itemsElm.scrollLeft + this.itemsElm.clientWidth < this.itemsElm.scrollWidth;
                this.backBtn = this.itemsElm.scrollLeft > 0;
            });
        },
        goLeft () {
            this.itemsElm.scrollLeft -= 40;
            this.checkBackForwardBtns();
        },
        goRight () {
            this.itemsElm.scrollLeft += 40;
            this.checkBackForwardBtns();
        },
        onScroll (e) {
            this.itemsElm.scrollLeft += e.wheelDelta > 0 ? +40 : -40;
            this.checkBackForwardBtns();
        },
        setScrollOnItem (itemOffsetLeft) {
            this.itemsElm.scrollLeft = itemOffsetLeft;
            this.checkBackForwardBtns();
        },
        createItem (folderId) {
            this.$store.commit(types.ADD_NEW_BB, {
                folder: folderId
            });
        },
        dropOnBar () {
            let items = Array.isArray(this.$store.state.dragItem.data) ?
                this.$store.state.dragItem.data : [this.$store.state.dragItem.data];
            switch (this.$store.state.dragItem.type) {
                case types.DRAG_ITEM_TYPE_FOLDER_BAR_ITEM:
                    this.$store.commit(types.BB_FOLDER_BAR_MOVE_ITEM, {
                        name: this.$store.state.dragItem.data.name,
                        toIndex: this.items.length - 1
                    });
                    break;
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    this.createItem(items[0].id);
                    // this.createItem(this.$store.state.dragItem.data.id);
                    break;
            }
        },
        dropOnAddButton () {
            switch (this.$store.state.dragItem.type) {
                case types.DRAG_ITEM_TYPE_FOLDER_BAR_ITEM:
                    this.createItem(this.$store.state.dragItem.data.currentFolderId);
                    break;
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    if(!Array.isArray(this.$store.state.dragItem.data)){
                        this.createItem(this.$store.state.dragItem.data.id);
                    }
                    break;
            }
        }
    }
}