// https://developer.chrome.com/extensions/

let b;
let isFirefox = false;
try {
    if(browser){
        b = browser;
        isFirefox = true;
    } else {
        b = chrome;
    }
} catch (error) {
    b = chrome;
}
// https://bugzilla.mozilla.org/show_bug.cgi?id=1315616 -_- FF dont give favicon // TODO
// const FAVICON_URL = isFirefox ? 'https://www.google.com/s2/favicons?domain_url=' : 'chrome://favicon/size/16@2x/';
const FAVICON_URL = isFirefox ? '' : 'chrome://favicon/size/16@2x/';
const BOOKMARKS_ROOT_FOLDER_ID = isFirefox ? 'root________' : '0';

export default {
    isFirefox,
    FAVICON_URL,
    BOOKMARKS_ROOT_FOLDER_ID,
    /* Bookmarks */
    moveBookmark (bookmarkId, destination) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.move(bookmarkId, destination, (bookmark) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(bookmark);
                }
            })
        });
    },
    createBookmark (createProperties) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.create(createProperties, (bookmark) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(bookmark);
                }
            })
        });
    },
    updateBookmark (bookmarkId, changes) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.update(bookmarkId, changes, (bookmark) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(bookmark);
                }
            })
        });
    },
    getBookmarkChildren (bookmarkId) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.getChildren(bookmarkId, (bookmarks) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(bookmarks);
                }
            })
        });
    },
    getBookmark (bookmarks) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.get(bookmarks, (bookmarks) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(bookmarks);
                }
            })
        });
    },
    removeBookmark (bookmarkId) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.remove(bookmarkId, () => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve();
                }
            })
        });
    },
    removeBookmarksTree (bookmarkId) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.removeTree(bookmarkId, () => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve();
                }
            })
        });
    },
    searchBookmarks (query) {
        return new Promise ((resolve, reject) => {
            b.bookmarks.search(query, (bookmarks) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(bookmarks);
                }
            })
        });
    },
    getBookmarksTree () {
        return new Promise ((resolve, reject) => {
            b.bookmarks.getTree((bookmarks) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(bookmarks);
                }
            })
        });
    },
    addListenerToBookmarks (event, handler) {
        if(b.bookmarks[event]){
            b.bookmarks[event].addListener(handler);
        }
    },
    isBookmarkFolder (bookmark) {
        return bookmark ? typeof bookmark.url === 'undefined' : undefined;
    },

    /* Tabs */
    createTab (createProperties) {
        return new Promise ((resolve, reject) => {
            b.tabs.create(createProperties, (tab) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(tab);
                }
            })
        });
    },
    updateTab (tabId, updateProperties) {
        return new Promise ((resolve, reject) => {
            b.tabs.update(tabId, updateProperties, (tab) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(tab);
                }
            })
        });
    },
    moveTabs (tabIds, moveProperties) {
        return new Promise ((resolve, reject) => {
            b.tabs.move(tabIds, moveProperties, (tabs) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(tabs);
                }
            })
        });
    },
    removeTabs (tabIds) {
        return new Promise ((resolve, reject) => {
            b.tabs.remove(tabIds, () => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve();
                }
            })
        });
    },
    getCurrentTab () {
        return new Promise ((resolve, reject) => {
            b.tabs.getCurrent((tab) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(tab);
                }
            })
        });
    },
    addListenerToTabs (event, handler) {
        if(b.tabs[event]){
            b.tabs[event].addListener(handler);
        }
    },

    /* Windows */
    createWindow (createProperties) {
        return new Promise ((resolve, reject) => {
            b.windows.create(createProperties, (window) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(window);
                }
            })
        });
    },
    updateWindow (windowId, updateProperties) {
        return new Promise ((resolve, reject) => {
            b.windows.update(windowId, updateProperties, (window) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(window);
                }
            })
        });
    },
    getAllWindows (getInfo) {
        return new Promise ((resolve, reject) => {
            b.windows.getAll(getInfo, (windows) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(windows);
                }
            })
        });
    },
    getCurrentWindow (getInfo) {
        return new Promise ((resolve, reject) => {
            b.windows.getCurrent(getInfo, (window) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(window);
                }
            })
        });
    },
    removeWindow (windowId) {
        return new Promise ((resolve, reject) => {
            b.windows.remove(windowId, () => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve();
                }
            })
        });
    },
    addListenerToWindows (event, handler) {
        if(b.windows[event]){
            b.windows[event].addListener(handler);
        }
    },

    /* Management */
    getExtensions () {
        return new Promise ((resolve, reject) => {
            b.management.getAll((extensions) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(extensions);
                }
            })
        });
    },
    launchApp (appId) {
        return new Promise ((resolve, reject) => {
            b.management.launchApp(appId, () => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve();
                }
            });
        });
    },

    /* Session */
    restoreSession (sessionId) {
        return new Promise ((resolve, reject) => {
            b.sessions.restore(sessionId, (session) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(session);
                }
            });
        });
    },
    getRecentlyClosed (filter) {
        return new Promise ((resolve, reject) => {
            b.sessions.getRecentlyClosed(filter, (sessions) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(sessions);
                }
            });
        });
    },
    getDevices (filter) {
        return new Promise ((resolve, reject) => {
            if(isFirefox){
                resolve([]);
            } else {
                b.sessions.getDevices(filter, (devices) => {
                    if(b.runtime.lastError){
                        console.log(b.runtime.lastError.message);
                        reject(b.runtime.lastError.message);
                    } else {
                        resolve(devices);
                    }
                });
            }
        });
    },
    addListenerToSessions (event, handler) {
        if(b.sessions[event]){
            b.sessions[event].addListener(handler);
        }
    },

    /* TopSites */
    getTopSites () {
        return new Promise ((resolve, reject) => {
            b.topSites.get((sites) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(sites);
                }
            });
        });
    },

    /* Runtime */
    getManifest () {
        return b.runtime.getManifest();
    },
    sendRuntimeMessage (message, responseCallback, options, extensionId) {
        if(message && responseCallback && options && extensionId){
            b.runtime.sendMessage(extensionId, message, options, responseCallback);
        } else if(message && responseCallback && options){
            b.runtime.sendMessage(message, options, responseCallback);
        } else if(message && responseCallback ){
            b.runtime.sendMessage(message, responseCallback);
        } else if(message){
            b.runtime.sendMessage(message);
        }
    },
    addListenerToRuntime (event, handler) {
        if(b.runtime[event]){
            b.runtime[event].addListener(handler);
        }
    },

    /* Commands */
    addListenerToCommands (event, handler) {
        if(b.commands[event]){
            b.commands[event].addListener(handler);
        }
    },

    /* Storage */
    getStorageData (storageArea, query) {
        return new Promise ((resolve, reject) => {
            b.storage[storageArea].get(query, (items) => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve(items);
                }
            });
        });
    },
    setStorageData (storageArea, items) {
        return new Promise ((resolve, reject) => {
            b.storage[storageArea].set(items, () => {
                resolve();
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                }
            });
        });
    },
    removeStorageData (storageArea, keys) {
        return new Promise ((resolve, reject) => {
            b.storage[storageArea].remove(keys, () => {
                if(b.runtime.lastError){
                    console.log(b.runtime.lastError.message);
                    reject(b.runtime.lastError.message);
                } else {
                    resolve();
                }
            });
        });
    },

}