import * as types from '../../store/mutation-types'
import createDragItem from '../drag_item/drag_item'
import brwsr from '../../services/browser'

export default {
    name: 'tree-item',
    props: [
        'model',
        'browser'
    ],
    data () {
        return {
            dragState: '',
            dragStateTypes: {
                before: 'before',
                after: 'after',
                into: 'in'
            },
            noChildEvents: false,
            childrenLoaded: false,
            middleMouseClicked: false
        }
    },
    created () {
        if(this.opened){
            this.childrenLoaded = true;
        }
        this.$watch('browser.currentFolderId', (newId, oldVal) => {
            if(this.id === newId){
                this.childrenLoaded = true;
                this.$emit('expandFolder');
                this.scrollTo();
            }
        });
    },
    mounted: function () {
        this.$root.$on('tree-started-rename', (bookmarkId) => {
            if(this.rename && this.id !== bookmarkId){
                this.renameFinish();
            }
        });
        this.$watch('model.treeForceRename', (newVal, oldVal) => {
            if(newVal){
                this.scrollTo();
                this.renameStart();
            }
        });
    },
    computed: {
        cssClasses () {
            return {
                'no-child-events': this.noChildEvents,
                'bm-first-folder': this.isRootFolder,
                'bm-folder-active': this.isCurrent,
                'bm-item-dragover': this.dropInFolder,
                'bm-item-dragover-before': this.dropBefore
            }
        },
        id () {
            return this.model.id;
        },
        title () {
            return this.model.title;
        },
        isCurrent () {
            return this.isBrowserActive && this.browser.currentFolderId === this.id;
        },
        isBrowserActive () {
            return this.browser.name === this.$store.state.activeBrowser;
        },
        opened () {
            return (this.isRootFolder) ? true : this.$store.state.openedFolders.indexOf(this.id) !== -1;
        },
        renameInput () {
            return this.$el.querySelector('.bm-title-edit');
        },
        isRootFolder () {
            return this.model.id === brwsr.BOOKMARKS_ROOT_FOLDER_ID;
        },
        empty () {
            return !this.model.children || this.model.children.length === 0;
        },
        // hide quick start folder in tree
        isQuickStartFolder () {
            return this.$store.state.quickStartFolder && this.$store.state.quickStartFolder.id === this.model.id;
        },
        dropBefore () {
            return this.dragState === this.dragStateTypes.before;
        },
        dropAfter () {
            return this.dragState === this.dragStateTypes.after;
        },
        dropInFolder () {
            return this.dragState === this.dragStateTypes.into;
        },
        dragItem () {
            return this.$store.state.dragItem;
        },
        clipboard () {
            return this.$store.state.clipboard;
        },
        rename () {
            return this.model.rename;
        }
    },
    methods: {
        browseFolder (e) {
            if(!this.middleMouseClicked){
                if(this.$store.state.activeBrowser !== this.browser.name){
                    this.$store.commit(types.SET_ACTIVE_BOOKMARKS_BROWSER, this.browser.name);
                }
                this.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
                    browserName: this.browser.name,
                    folderId: this.model.id
                });
            }
        },
        onMouseDown (e) {
            if(e.button === 1){
                this.$store.commit(types.ADD_NEW_BB, {folder: this.model.id});
                e.preventDefault();
            }
        },
        scrollTo () {
            let list = document.getElementById('tree-column');
            let elm = this.$el;
            this.$nextTick(function () {
                let listRect = list.getBoundingClientRect();
                let elmRect = elm.getBoundingClientRect();
                if((elmRect.bottom - listRect.top) > listRect.height){
                    elm.scrollIntoView();
                } else if(elmRect.top < 0) {
                    elm.scrollIntoView(true);
                }
                // elm.scrollIntoView({
                 //    block: 'center'
                // }); // TODO;
            });
            if(this.id == 0){
                list.scrollTop = 0;
            } // TODO
        },
        toggle () {
            if(!this.childrenLoaded){
                this.childrenLoaded = true;
            }
            if(this.id){
                this.$store.commit(types.TREE_CHANGE_FOLDER_STATE, this.id);
            }
        },
        expandFolder () {
            if(!this.opened && !this.empty){
                this.toggle();
                if(this.model.parentId){
                    this.$emit('expandFolder');
                }
            }
        },
        dragStart (e) {
            // e.dataTransfer.setDragImage(createDragItem(this.model.url, this.model.title, true), -20, 0);
            this.$store.commit(types.SET_DRAG_ITEM, {
                type: this.model.hasOwnProperty('url') ? types.DRAG_ITEM_TYPE_BOOKMARK : types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER,
                startEvent: e,
                data: this.model
            });
        },
        dragEnter (e) {
            // http://stackoverflow.com/questions/12509602/html5-is-there-a-way-to-prevent-children-interfering-with-drag-events
            if(e.target.classList.contains('bm-item')){
                this.noChildEvents = true;
            }
        },
        dragOver (e) {
            if(this.dragItem.type === types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER ||
                (this.dragItem.type === types.DRAG_ITEM_TYPE_BOOKMARK && Array.isArray(this.dragItem.data))){
                let rect = e.target.getBoundingClientRect();
                if(e.offsetY >= rect.height*20/100){
                    this.dragState = this.dragStateTypes.into;
                } else {
                    this.dragState = this.dragStateTypes.before;
                }
            } else {
                this.dragState = this.dragStateTypes.into;
            }
        },
        dragLeave (e) {
            if(e.target.classList.contains('bm-item')){
                this.noChildEvents = false;
            }
            this.dragState = '';
        },
        drop (e) {
            let parentId = this.model.id;
            let items = Array.isArray(this.dragItem.data) ? this.dragItem.data : [this.dragItem.data];
            if(this.dropInFolder){
                switch (this.dragItem.type) {
                    case types.DRAG_ITEM_TYPE_BOOKMARK:
                    case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                        for(let i = 0; i < items.length; i++){
                            if(items[i].id !== parentId){
                                brwsr.moveBookmark(items[i].id, {
                                    parentId: parentId
                                });
                            }
                        }
                        break;
                    case types.DRAG_ITEM_TYPE_TAB:
                    case types.DRAG_ITEM_TYPE_RECENT:
                    case types.DRAG_ITEM_TYPE_TOP_SITE:
                        for(let i = 0; i < items.length; i++){
                            brwsr.createBookmark({
                                title: items[i].title,
                                url: items[i].url,
                                parentId: parentId,
                            });
                        }
                        break;
                    case types.DRAG_ITEM_TYPE_BROWSER_WINDOW:
                        const currentDate = new Date();
                        const tabs = this.dragItem.data.tabs;
                        brwsr.createBookmark({
                            title: 'Window '+ currentDate.toLocaleString(),
                            parentId: parentId
                        }).then((folder) => {
                            tabs.forEach((tab, index) => {
                                window.setTimeout(() => {
                                    brwsr.createBookmark({
                                        title: tab.title,
                                        url: tab.url,
                                        parentId: folder.id
                                    });
                                }, index * 50);
                            });
                        });
                        break;
                }
            }
            if(this.dropBefore){
                brwsr.getBookmark(this.model.id).then((bookmarks) => {
                    if(bookmarks.length){
                        for(let i = 0; i < items.length; i++){
                            if(!items[i].hasOwnProperty('url')){
                                brwsr.moveBookmark(items[i].id, {
                                    parentId: bookmarks[0].parentId,
                                    index: bookmarks[0].index
                                });
                            }
                        }
                    }
                });
            }
            this.$store.commit(types.RESET_DRAG_ITEM);
            this.noChildEvents = false;
            this.dragState = '';
        },
        renameStart () {
            this.$set(this.model, 'rename', true);
            this.$set(this.model, 'oldTitle', this.model.title);
            this.$nextTick( () => {
                this.renameInput.focus();
                this.renameInput.select();
            });
            window.addEventListener('click', this.renameFinish);
        },
        renameFinish () {
            if(this.rename){
                this.model.rename = false;
                brwsr.updateBookmark(this.id, {title: this.model.title});
                window.removeEventListener('click', this.renameFinish);
            }
        },
        renameCancel () {
            if(this.rename){
                this.model.rename = false;
                this.model.title = this.model.oldTitle;
                window.removeEventListener('click', this.renameFinish);
            }
        },
        showContextMenu (e) {
            let items = [];
            items.push({
                title: 'Open in Folder bar',
                vm: this,
                clickFn: function (item, menu) {
                    item.vm.$store.commit(types.ADD_NEW_BB, {folder: item.vm.model.id});
                    menu.hide();
                }
            });
            items.push({
                title: 'Create Window',
                vm: this,
                clickFn: function (item, menu) {
                    brwsr.getBookmarkChildren(item.vm.model.id).then((bookmarks) => {
                        if(bookmarks.length) {
                            let urls = [];
                            for(let i=0; i < bookmarks.length; i++ ){
                                if(bookmarks[i].hasOwnProperty('url')){
                                    urls.push(bookmarks[i].url);
                                }
                            }
                            brwsr.createWindow({
                                url: urls,
                                state: 'maximized'
                            });
                        }
                    });
                    menu.hide();
                }
            });
            items.push({
                title: 'Cut',
                vm: this,
                clickFn: function (item, menu) {
                    item.vm.$store.commit(types.ADD_TO_CLIPBOARD, [item.vm.id]);
                    menu.hide();
                }
            });
            items.push({
                title: 'Paste',
                vm: this,
                inactive: this.clipboard.length === 0,
                clickFn: function (item, menu) {
                    let clipboard = item.vm.clipboard;
                    if(item.vm.clipboard.length > 0){
                        for(let i=0; i< clipboard.length; i++){
                            if(clipboard[i] !== item.vm.id){
                                brwsr.moveBookmark(clipboard[i], {
                                    parentId: item.vm.id
                                }).then((bookmark) => {
                                    item.vm.$store.commit(types.REMOVE_FROM_CLIPBOARD, bookmark.id); // TODO ?
                                });
                            }
                        }
                        menu.hide();
                    }
                }
            });
            items.push({
                title: 'Rename',
                vm: this,
                clickFn: function (item, menu) {
                    item.vm.$root.$emit('tree-started-rename', item.vm.id);
                    item.vm.renameStart();
                    menu.hide();
                }
            });
            items.push({
                title: 'Delete',
                vm: this,
                confirm: false,
                inactive: this.isQuickStartFolder,
                clickFn: function (item, menu) {
                    if(!item.confirm){
                        item.confirm = true;
                    } else {
                        let bookmark = item.vm.model;
                        if(bookmark.hasOwnProperty('url')){
                            brwsr.removeBookmark(bookmark.id);
                        } else {
                            brwsr.removeBookmarksTree(bookmark.id);
                        }
                        menu.hide();
                    }
                }
            });
            items.push({
                title: 'Add folder',
                vm: this,
                clickFn: function (item, menu) {
                    brwsr.createBookmark({
                        parentId: item.vm.id,
                        title: 'New folder'
                    }).then((bm) => {
                        window.setTimeout( () => {
                            if(!item.vm.childrenLoaded){
                                item.vm.childrenLoaded = true;
                            }
                            if(!item.vm.opened){
                                item.vm.toggle();
                            }
                            item.vm.$nextTick( () => {
                                let bmModel = item.vm.model.children.find( i => i.id === bm.id);
                                item.vm.$set(bmModel, 'treeForceRename', true);
                            });
                        }, 150);
                    });
                    menu.hide();
                }
            });
            items.push({
                title: 'Add bookmark',
                vm: this,
                hr: true,
                clickFn: function (item, menu) {
                    item.vm.$root.$emit('dialog-create-bookmark', item.vm.id);
                    menu.hide();
                }
            });
            items.push({
                title: 'Set as Quick Start',
                vm: this,
                clickFn: function (item, menu) {
                    const bookmark = item.vm.model;
                    item.vm.$store.commit(types.SET_QUICK_START_FOLDER, bookmark);
                    item.vm.$store.commit(types.BB_SET_TOP_LEVEL_ID, {
                        browserName: item.vm.$store.state.browsers[0].name,
                        bookmarkId: bookmark.id
                    });
                    item.vm.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
                        browserName: item.vm.$store.state.browsers[0].name,
                        folderId: bookmark.id
                    }); // TODO double code
                    menu.hide();
                }
            });
            this.$root.$emit('show-context-menu', {
                left: e.pageX,
                top: e.pageY,
                items: items
            })
        }
    }
}