export default {
    // test algorithm, based on https://vc.ru/p/yandex-colour
    getMainColor (image) {
        let canvas  = document.createElement('canvas');
        canvas.width = 2;
        canvas.height = 2;

        let context = canvas.getContext('2d');
        context.drawImage(image, 0, 0, 2, 2);

        let rgbColors = [];
        rgbColors[0] = context.getImageData(0, 0, 1, 1).data;
        rgbColors[1] = context.getImageData(1, 0, 1, 1).data;
        rgbColors[2] = context.getImageData(0, 1, 1, 1).data;
        rgbColors[3] = context.getImageData(1, 1, 1, 1).data;

        let hlsColors = [];
        hlsColors[0] = this.convertRgbToHsl(rgbColors[0][0], rgbColors[0][1], rgbColors[0][2]);
        hlsColors[1] = this.convertRgbToHsl(rgbColors[1][0], rgbColors[1][1], rgbColors[1][2]);
        hlsColors[2] = this.convertRgbToHsl(rgbColors[2][0], rgbColors[2][1], rgbColors[2][2]);
        hlsColors[3] = this.convertRgbToHsl(rgbColors[3][0], rgbColors[3][1], rgbColors[3][2]);

        let sortLight = 0;
        for(let i=0; i < hlsColors.length; i++){
            if(hlsColors[sortLight][2] < hlsColors[i][2]){
                sortLight = i;
            }
        }

        rgbColors[sortLight][3] = 1; // opacity
        return rgbColors[sortLight];
    },
    getImageColorState (img) {
        let canvas  = document.createElement('canvas'),
            context = canvas.getContext('2d'),
            colorData,
            rgb = {r: 0, g:0, b:0},
            light = 0,
            dark = 0;

        canvas.width = 16;
        canvas.height = 16;
        context.drawImage(img, 0, 0, 16, 16);
        colorData = context.getImageData(0, 0, 16, 16).data;

        let i = -4;
        while ((i += 4) < colorData.length) {
            rgb.r += colorData[i];
            rgb.g += colorData[i+1];
            rgb.b += colorData[i+2];
            this.convertRgbToHsl(colorData[i], colorData[i+1], colorData[i+2])[2] > 0.5 ? light ++ : dark ++;
            // console.log('r '+ colorData[i] +' g '+ colorData[i+1] +' b '+ colorData[i+2]);
        }
        // ~~ used to floor values
        rgb.r = ~~(rgb.r/256);
        rgb.g = ~~(rgb.g/256);
        rgb.b = ~~(rgb.b/256);

        return {
            rgb: rgb,
            light: light,
            dark: dark,
            gamma: dark !== 0 ? light/dark : 2
        }
    },
    // http://axonflux.com/handy-rgb-to-hsl-and-rgb-to-hsv-color-model-c
    convertRgbToHsl (r, g, b) {
        r /= 255;
        g /= 255;
        b /= 255;
        let max = Math.max(r, g, b),
            min = Math.min(r, g, b);
        let h, s, l = (max + min) / 2;

        if(max == min){
            h = s = 0; // achromatic
        } else {
            let d = max - min;
            s = l > 0.5 ? d / (2 - max - min) : d / (max + min);
            switch(max){
                case r: h = (g - b) / d + (g < b ? 6 : 0); break;
                case g: h = (b - r) / d + 2; break;
                case b: h = (r - g) / d + 4; break;
            }
            h /= 6;
        }
        return [h, s, l];
    }
}