import * as types from '../../store/mutation-types'
import brwsr from '../../services/browser'

export default {
    props: [
        'model',
        'container',
        'index',
        'lastInList'
    ],
    data () {
        return {
            closing: false,
            noChildEvents: false,
            activationTimeout: null,
            elmRect: null,
            offsetX: 0
        }
    },
    mounted () {
        window.setTimeout(()=>{
            this.checkVisible();
        },150);
        this.$watch('$store.state.columns', () => {
            this.checkVisible();
        }, {
            deep: true
        });
        this.$watch('isActive', () => {
            this.checkVisible();
        });
        window.addEventListener('resize', this.checkVisible);
    },
    beforeDestroy () {
        window.removeEventListener('resize', this.checkVisible);
    },
    computed: {
        classes () {
            return {
                'menu-bar-item-active': this.isActive,
                'menu-bar-item-closing': this.closing,
                'menu-bar-item-drop-before': this.dropPosition === 'before',
                'menu-bar-item-drop-after': this.dropPosition === 'after',
                'no-child-events': this.noChildEvents,
            }
        },
        isActive () {
            return this.model.name === this.$store.state.activeBrowser;
        },
        isQuickStart () {
            return this.model.quickStart && this.model.topLevelId === this.model.currentFolderId;
        },
        dragItem () {
            return this.$store.state.dragItem;
        },
        dropPosition () {
            if (this.elmRect && !this.model.permanent) {
                if (this.offsetX >= 1 && this.offsetX <= 20) {
                    window.clearTimeout(this.activationTimeout);
                    return 'before';
                } else if(this.offsetX >= this.elmRect.width - 20 && this.offsetX <= this.elmRect.width) {
                    window.clearTimeout(this.activationTimeout);
                    return 'after';
                }
            }
            if(this.offsetX !== 0){
                this.tryToSetActive();
            }
            return '';
        }
    },
    methods: {
        setActive () {
            this.$store.commit(types.SET_ACTIVE_BOOKMARKS_BROWSER, this.model.name);
        },
        tryToSetActive () {
            window.clearTimeout(this.activationTimeout);
            this.activationTimeout = window.setTimeout(() => {
                this.setActive();
            }, 600);
        },
        removeBrowser () {
            this.closing = true;
            window.setTimeout(()=>{
                this.$root.$emit('close-bookmark-browser', this.model.name);
            }, 150);
        },
        checkVisible () {
            if(this.isActive){
                if(this.$el.offsetLeft + this.$el.clientWidth >= this.container.scrollLeft + this.container.clientWidth){
                    this.$emit('scrollOnMe', this.$el.offsetLeft);
                }
                else if(this.container.scrollLeft + 32 > 0 && this.$el.offsetLeft < this.container.scrollLeft){
                    this.$emit('scrollOnMe', this.$el.offsetLeft - 32);
                }
            }
        },
        onMouseDown (e) {
            if(e.button === 1 && !this.model.permanent){
                this.removeBrowser();
                e.preventDefault();
            }
        },
        itemDragStart (e) {
            this.$store.commit(types.SET_DRAG_ITEM, {
                type: types.DRAG_ITEM_TYPE_FOLDER_BAR_ITEM,
                startEvent: e,
                // name: this.model.name,
                data: this.model
            });
        },
        itemDragEnter (e) {
            this.elmRect = null;
            if(e.target.classList.contains('menu-bar-item')){
                this.noChildEvents = true;
            }
            switch (this.dragItem.type) {
                case types.DRAG_ITEM_TYPE_FOLDER_BAR_ITEM:
                    this.$store.commit(types.BB_FOLDER_BAR_MOVE_ITEM, {
                        name: this.dragItem.data.name,
                        toIndex: this.index
                    });
                    break;
                case types.DRAG_ITEM_TYPE_BOOKMARK:
                case types.DRAG_ITEM_TYPE_TAB:
                case types.DRAG_ITEM_TYPE_RECENT:
                case types.DRAG_ITEM_TYPE_TOP_SITE:
                    this.tryToSetActive();
                    break;
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    this.elmRect = e.target.getBoundingClientRect();
                    this.tryToSetActive();
                    break;
            }
        },
        itemDragOver (e) {
            switch (this.dragItem.type) {
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    this.offsetX = e.offsetX;
                    break;
            }
        },
        itemDragLeave (e) {
            window.clearTimeout(this.activationTimeout);
            this.offsetX = 0;
            if(e.target.classList.contains('menu-bar-item')){
                this.noChildEvents = false;
            }
        },
        itemDrop () {
            window.clearTimeout(this.activationTimeout);
            let items = Array.isArray(this.dragItem.data) ? this.dragItem.data : [this.dragItem.data];
            let parentId = this.model.currentFolderId;
            switch (this.dragItem.type) {
                case types.DRAG_ITEM_TYPE_BOOKMARK:
                    for(let i = 0; i < items.length; i++){
                        if(items[i].parentId !== parentId && items[i].id !== parentId){
                            brwsr.moveBookmark(items[i].id, {
                                parentId: parentId
                            });
                        }
                    }
                    break;
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    if (this.dropPosition) {
                        if(!items[0].hasOwnProperty('url')){
                            this.$store.commit(types.ADD_NEW_BB, {
                                folder: items[0].id,
                                toIndex: this.dropPosition === 'before' ? this.index : this.index + 1
                            });
                        } // TODO multi?
                    } else {
                        for(let i = 0; i < items.length; i++){
                            if(items[i].parentId !== parentId && items[i].id !== parentId){
                                brwsr.moveBookmark(items[i].id, {
                                    parentId: parentId
                                });
                            }
                        }
                    }
                    break;
                case types.DRAG_ITEM_TYPE_TAB:
                case types.DRAG_ITEM_TYPE_RECENT:
                case types.DRAG_ITEM_TYPE_TOP_SITE:
                    for(let i = 0; i < items.length; i++){
                        brwsr.createBookmark({
                            url: items[i].url,
                            title: items[i].title,
                            parentId: parentId
                        });
                    }
                    break;
            }
            this.$store.commit(types.RESET_DRAG_ITEM);
            this.offsetX = 0;
            this.elmRect = null;
            this.noChildEvents = false;
        },
    }
}