import brwsr from '../../services/browser'

export default {
props: [],
data () {
    return {
        text: {
            '0.7.0.3': [
                'Interface new concept: Bar with folders',
                'Keyboard control for central zone. Available keys: Arrows, Home, End, Enter, Enter+Ctrl - Context menu, Ctrl+X - Cut, Ctrl+V - Paste, Backspace - Back',
                'Bookmark Folder > Context menu > Create Window - Make new window with folder`s bookmarks',
                'Bookmark Link > Context menu > Open in background - Make new background tab (also you can use Middle mouse click)',
                'Drag`n`drop: New concept (alpha)',
                'Search: UI and behavior fixes',
                'Settings UI fixes',
                'Settings: Added Unsplash.com for background (alpha | unstable)',
                'Other bugs fixes',
                '0.7.0.3: Fixed bug with [+] button',
                '0.7.0.2: Single click returned',
                'Still beta. Let`s test:)'
            ],
            '0.7.1': [
                'New! Select the search engine for Search bar',
                'New! Tabs Module (beta) for right column. You can easy drag`n`drop tabs and windows from Tabs Module.',
                'Some fixes'
            ],
            '0.7.2': [
                '- [ Experimental ] Explore the new editing options (Bookmark Link - Context menu - Edit). There you can set custom title for tile, icon or full override tile with image. Overrides Manager can help you to export/import your overrides to another computer.',
                '- [ New ] Show/hide Search (the toggle above the search field).',
                '- Some fixes'
            ],
            '0.7.3': [
                '- Bug fix after Chrome update',
            ],
            '1.0.0': [
                '- Added choice for what you want to see in left and right columns',
                '- Search results UI fixes',
                '- Added Context menu for Search results',
                '- Context menu: Added "Set as Quick start" for folders',
                '- Context menu: Added "Copy to clipboard" for links',
                '- Mouse middle click work for: Search results, Folder bar items, Tabs, Folders and links',
                '- Experimental: Multiple select with Ctrl or Shift keys for Bookmarks and Tabs',
                '- Drag\'n\'drop work better (Folders bar, Bookmarks, Windows, Tabs)',
                '- Folders can remember the views. Use the mouse context menu button instead of primary mouse button to select and save a view',
                '- New Settings UI',
                '- Other changes'
            ]
        },
        display: true,
        closing: false
    }
},
computed: {
    version () {
        return brwsr.getManifest().version;
    },
    items () {
        return this.text[this.version] ? this.text[this.version] : [];
    },
    load () {
        if(this.text[this.version]){
            return this.$store.state.whatIsNewAvailable;
        }
        return false;
    }
},
methods: {
    close () {
        this.closing = true;
        window.setTimeout(()=>{
            this.display = false;
        }, 400);
    }
}
}