import * as types from '../../store/mutation-types'
export default {
	props: [
		'browser',
		'viewTypes',
		'currentView'
	],
	computed: {
		isGrid: function () {
			return this.currentView === this.viewTypes.grid
		},
		isList: function () {
			return this.currentView === this.viewTypes.list
		},
		isTile: function () {
			return this.currentView === this.viewTypes.tile
		},
		isCompact: function () {
			return this.currentView === this.viewTypes.compact
		}
	},
	methods: {
		setBrowserView (view) {
			this.$store.commit(types.BB_CHANGE_VIEW, {
				browserName: this.browser.name,
				view: view
			});
			this.$store.commit(types.DELETE_FOLDERS_CONFIG_PROP, {
				folderId: this.browser.currentFolderId,
				prop: 'view',
			});
		},
		setFolderView (view, e) {
			this.$store.commit(types.SET_FOLDERS_CONFIG, {
				folderId: this.browser.currentFolderId,
				prop: 'view',
				value: view
			});
		}
	}
}