import * as types from '../../store/mutation-types'
export default {
	data () {
		return {
			spaceName: 'context_menu',
			prevActiveSpace: '',
			display: false,
			items: [],
            focusOnIndex: 0
		}
	},
	computed: {
        isSpaceActive () {
            return this.spaceName === this.$store.state.activeSpace;
        }
	},
	created () {
		this.$root.$on('show-context-menu', (data) => {			
			this.show(data);
		});
	},
	methods: {
		show (menuData) {
            menuData.keyboard ? this.focusOnIndex = 0 : this.focusOnIndex = -1;
			this.prevActiveSpace = this.$store.state.activeSpace;
			this.items = menuData.items;
			this.display = true;
			let menu = this.$el;
			this.$nextTick( () => {
				let menuRect = menu.getBoundingClientRect();
				if(menuData.left + menuRect.width > window.innerWidth){
					menuData.left -= menuRect.width;
				}
				if(menuData.top + menuRect.height > window.innerHeight){
					menuData.top -= menuRect.height;
				}
				menu.style.left = menuData.left +'px';
				menu.style.top = menuData.top +'px';
			});
			window.document.body.addEventListener('click', this.hide);
            this.$store.commit(types.SET_ACTIVE_SPACE, this.spaceName);
            window.addEventListener('keydown', this.onKeyDown);
		},
		hide () {
			this.display = false;
			window.document.body.removeEventListener('click', this.hide);
            window.removeEventListener('keydown', this.onKeyDown);
            if(this.focusOnIndex < 0 || !this.items[this.focusOnIndex].vm.model.rename){
                this.$store.commit(types.SET_ACTIVE_SPACE, this.prevActiveSpace);
			}
            this.items = [];
		},
		onItemClick (item) {
			if(!item.inactive && typeof item.clickFn === 'function'){
                item.clickFn(item, this);
			}
		},
		onKeyDown (e) {
			if(this.isSpaceActive){
				switch (e.code) {
                    case 'ArrowUp':
                        if(this.focusOnIndex > 0){
                            this.focusOnIndex--;
                        }
                        e.returnValue = false;
                        break;
                    case 'ArrowDown':
                        if(this.focusOnIndex < this.items.length-1){
                            this.focusOnIndex++;
                        }
                        e.returnValue = false;
                        break;
                    case 'Enter':
                        this.onItemClick(this.items[this.focusOnIndex]);
                        e.returnValue = false;
                        break;
					case 'Escape':
						this.hide();
                        e.returnValue = false;
                        break;
                }
			}
		}
	}
}