import Vue from 'vue'
import * as types from '../../store/mutation-types'
import SearchItem from './search_item.vue'
import brwsr from '../../services/browser'

export default {
    components: {
        SearchItem
    },
    created () {
        this.$store.commit(types.RESTORE_SEARCH_ENGINE);
    },
    data () {
        return {
            spaceName: 'search',
            items: [],
            text: '',
            currentIndex: -1,
            maxResultsInDropField: 5,
            focusOnParentFolder: false,
            searchTimeoutId: 0,
            engines: [
                {
                    id: 'ufblf',
                    title: 'Bookmarks'
                },{
                    id: 'rka0w',
                    title: 'Google',
                    url: 'https://www.google.com/search?q='
                },{
                    id: 'wf268',
                    title: 'Google Images',
                    url: 'https://www.google.com/search?site=imghp&tbm=isch&source=hp&q='
                },{
                    id: 'z2z4v',
                    title: 'Google Video',
                    url: 'https://www.google.com/search?site=imghp&tbm=vid&q='
                },{
                    id: 'f03bv',
                    title: 'Google Maps',
                    url: 'https://www.google.com/maps?q='
                },{
                    id: '695zz',
                    title: 'YouTube',
                    url: 'https://www.youtube.com/search?q='
                },{
                    id: '695z1',
                    title: 'Bing',
                    url: 'https://www.bing.com/search?q='
                },{
                    id: 'p5lzu',
                    title: 'Wikipedia',
                    url: 'https://en.wikipedia.org/w/index.php?search='
                },{
                    id: 'ctjln',
                    title: 'DuckDuckGo',
                    url: 'https://duckduckgo.com/?q='
                },{
                    id: 'kgfrp',
                    title: 'Yandex',
                    url: 'https://yandex.com/search/?text='
                }
            ]
        }
    },
    computed: {
        input () {
            return this.$el.querySelector('.search-bar-input');
        },
        resultOverflow () {
            return this.items.length > this.maxResultsInDropField;
        },
        overflowItemActive () {
            return this.currentIndex >= this.maxResultsInDropField;
        },
        searchResult () {
            return this.items.length > 0;
        },
        activeBrowserName () {
            return this.$store.state.activeBrowser;
        },
        searchEngineId: {
            get () {
                return this.$store.state.searchEngineId;
            },
            set (val) {
                this.$store.commit(types.CHANGE_SEARCH_ENGINE, {id: val});
                this.checkEngine();
            }
        },
        searchEngineTitle () {
            return this.engines.find(e => e.id === this.searchEngineId).title;
        }
    },
    methods: {
        find () {
            window.clearTimeout(this.searchTimeoutId);
            if(this.text.length >= 2){
                this.searchTimeoutId = window.setTimeout(() => {
                    brwsr.searchBookmarks(this.text).then((bookmarks) => {
                        window.addEventListener('keydown', this.onKey);
                        window.addEventListener('click', this.onClickOutside);
                        this.items = bookmarks;
                        this.getParentFolder(this.items);
                        this.currentIndex = -1;
                    });
                }, 100);
            } else if(this.text.length === 0) {
                this.resetInput(true);
            }
        },
        onKey (e) {
            switch (e.key || e.code) {
                case 'ArrowDown':
                    if(this.searchResult && this.currentIndex < this.maxResultsInDropField) {
                        this.input.blur();
                        this.currentIndex++;
                        this.focusOnParentFolder = false;
                    }
                    break;
                case 'ArrowUp':
                    if(this.searchResult && this.currentIndex > -1) {
                        this.currentIndex--;
                        this.focusOnParentFolder = false;
                    }
                    if(this.currentIndex === -1) {
                        this.input.focus();
                        window.setTimeout( () => {
                            this.input.select();
                        }, 10);
                    }
                    break;
                case 'ArrowRight':
                    if(this.currentIndex > -1){
                        this.focusOnParentFolder = true;
                    }
                    break;
                case 'ArrowLeft':
                    if(this.currentIndex > -1){
                        this.focusOnParentFolder = false;
                    }
                    break;
                case 'Enter':
                    if(this.currentIndex > -1 && this.currentIndex < this.maxResultsInDropField){
                        let item = this.items[this.currentIndex];
                        this.openItem(item);
                    } else if(this.currentIndex === this.maxResultsInDropField){
                        this.openMoreResults();
                    } else if(this.currentIndex === -1){
                        this.checkEngine();
                    }
                    break;
                case 'Escape':
                    this.resetInput();
                    break;
                case 'Home':
                    if(this.currentIndex > -1){
                        this.currentIndex = -1;
                        this.input.focus();
                        window.setTimeout( () => {
                            this.input.select();
                        }, 10);
                    }
                    break;
                case 'End':
                    if(this.searchResult){
                        this.input.blur();
                        this.currentIndex = this.maxResultsInDropField;
                    }
                    break;
            }
        },
        resetInput (withoutBlur) {
            this.currentIndex = -1;
            this.items = [];
            this.text = '';
            if(!withoutBlur){
                this.input.blur();
            }
            this.removeListeners();
        },
        onInputFocus () {
            this.$store.commit(types.SET_ACTIVE_SPACE, this.spaceName);
            if(this.input.value){
                this.input.select();
            }
            this.currentIndex = -1;
        },
        onClickOutside (e) {
            this.currentIndex = -1;
            this.items = [];
            this.removeListeners();
            e.stopPropagation();
        },
        removeListeners () {
            window.removeEventListener('keydown', this.onKey);
            window.removeEventListener('click', this.onClickOutside);
        },
        checkEngine () {
            if(this.text){
                let engine = this.engines.find( e => e.id === this.searchEngineId);
                if (engine.url) {
                    window.location.replace(engine.url + window.encodeURIComponent(this.text));
                } else {
                    this.openMoreResults();
                }
            }
        },
        openMoreResults () {
            if(this.text.length > 0){
                this.$store.commit(types.ADD_NEW_BB, {
                    searchResult: {
                        text: this.text,
                        items: this.items
                    }
                });
                this.resetInput();
            }
        },
        openItem (item) {
            if(this.focusOnParentFolder){
                this.openFolder(item.parentId, item.id);
            } else {
                if(brwsr.isBookmarkFolder(item)){
                    this.openFolder(item.id);
                } else {
                    window.location.replace(item.url);
                }
            }
        },
        openFolder (id, focusOnItem) {
            this.$store.commit(types.ADD_NEW_BB, {folder: id});
            if(focusOnItem > 0){
                this.$store.commit(types.BB_SET_FOCUS_ON_ITEM, {
                    browserName: this.activeBrowserName,
                    bookmarkId: focusOnItem
                });
            }
            this.$store.commit(types.SET_ACTIVE_SPACE_BY_BROWSER_NAME, this.activeBrowserName);
            this.resetInput();
        },
        getParentFolder (bookmarks) {
            for(let i=0; bookmarks.length > this.maxResultsInDropField ? i < this.maxResultsInDropField : i < bookmarks.length; i++){
                brwsr.getBookmark(bookmarks[i].parentId).then((folders) => {
                    Vue.set(bookmarks[i], 'folderName', folders[0].title);
                });
            }
        }
    }
}
