import * as types from '../../store/mutation-types'
// import createDragItem from '../drag_item/drag_item'
import brwsr from '../../services/browser'
import colorHelper from '../../helpers/color'

export default {
    props: [
        'index',
        // 'model',
        'items',
        'browser',
        'view',
        'viewTypes',
        'selectedItems',
        'tileDecor',
        'decor'
    ],
    data () {
        return {
            mainColor: '',
            noChildEvents: false,
            dragInProgress: false, // TODO del
            elmRect: null,
            offsetX: 0,
            offsetY: 0,
            playFolderChangedAnim: false,
            animTimer: null
        }
    },
    computed: {
        classes () {
            return {
                // 'no-child-events': this.noChildEvents,
                'no-child-events': !this.rename,
                'browser-item-link': this.isLink,
                'browser-item-folder': !this.isLink,
                'browser-item-focused': this.isFocused,
                'browser-item-keyboard-focused': this.isKeyboardFocused,
                'browser-item-selected': this.isSelected,
                'browser-item-cuted': this.inClipboard,
                'browser-item-quick-start-folder': this.isQuickStartFolder,
                'browser-dragover-before': this.dropPosition === 'before',
                'browser-dragover-after': this.dropPosition === 'after',
                'browser-dragover-folder': this.dropPosition === 'into',
                'browser-item-folder-updated-anim': !this.isLink && this.playFolderChangedAnim
            }
        },
        styles () {
            return {
                'background-color': this.tileBgColor,
                'border-color': this.tileBorderColor,
                'margin-bottom': this.isCompactView && this.decor.text.compactViewLineSpacingSize > 0 ? this.decor.text.compactViewLineSpacingSize + 'px' : ''
            }
        },
        model () {
            return this.items[this.index];
        },
        id () {
            return this.model.id;
        },
        isLink () {
            return !brwsr.isBookmarkFolder(this.model);
        },
        isFocused () {
            return this.id === this.browser.focusOnId;
        },
        isKeyboardFocused () {
            return typeof this.browser.focusOnIndex !== 'undefined' ? this.index === this.browser.focusOnIndex : false;
        },
        isSelected () {
            return this.selectedItems.indexOf(this.model.index) !== -1;
        },
        isCompactView () {
            return this.view === this.viewTypes.compact;
        },
        isListView () {
            return this.view === this.viewTypes.list;
        },
        isTileView () {
            return this.view === this.viewTypes.tile;
        },
        isTilePicOverridden () {
            if(!this.isTileView || this.rename || !this.override.tilePicUrl){
                return false;
            } else if(this.isTileView && this.override.tilePicUrl){
                return true;
            }
            return false;
        },
        isIconOverridden () {
            return !!this.override.iconUrl;
        },
        isTileTitleOverridden () {
            return !!this.override.tileTitle;
        },
        imgFavicon () {
            return this.isLink ?
                this.$el.querySelector('.browser-item-icon') :
                this.$el.querySelector('.browser-item-folder-icon');
        },
        icon () {
            if(this.override.iconUrl){
                return this.override.iconUrl;
            } else {
                return !brwsr.isFirefox ? brwsr.FAVICON_URL + this.model.url : 'https://' + this.domain + '/favicon.ico';
                // return brwsr.FAVICON_URL + this.model.url;
            }
        },
        renameInput () {
            return this.$el.querySelector('.browser-item-title-edit');
        },
        hostname () {
            if(this.model.url){
                let link = document.createElement('a');
                link.href = this.model.url;
                // var host = this.model.url.trim().match(/(?!(w+)\.)\w*(?:\w+\.)+\w+/gm)[0];
                // return host.substring(0, host.indexOf('.'));
                let host = link.hostname.indexOf('www.') === -1 ? link.hostname : link.hostname.substring(4);
                return host.indexOf('.') === -1 ? host : host.substring(0, host.indexOf('.')); // TODO
            }
            return '';
        },
        domain () {
            if(this.isLink){
                let link = document.createElement('a');
                link.href = this.model.url;
                return link.hostname;
            }
            return '';
        },
        hasTitle () {
            return this.model.title.length > 0;
        },
        isQuickStartFolder () {
            return this.$store.state.quickStartFolder && this.$store.state.quickStartFolder.id === this.model.id;
        },
        inClipboard () {
            return this.$store.state.clipboard.indexOf(this.id) !== -1;
        },
        isCustomize () {
            return this.tileDecor.backgroundColor.length || this.tileDecor.borderColor.length;
        },
        tileBgColor () {
            return this.isTileView && this.isCustomize ? 'rgba('+ this.tileDecor.backgroundColor +')' : '';
        },
        tileBorderColor () {
            return this.isTileView && this.isCustomize ? 'rgba('+ this.tileDecor.borderColor +')' : '';
        },
        dragItem () {
            return this.$store.state.dragItem;
        },
        clipboard () {
            return this.$store.state.clipboard;
        },
        rename () {
            return this.model.rename;
        },
        override () {
            return {
                tileTitle: this.$store.state.tileTitleOverrides[this.model.url],
                iconUrl: this.$store.state.iconOverrides[this.model.url] || this.$store.state.iconOverrides[this.domain],
                tilePicUrl: this.$store.state.tilePicOverrides[this.model.url]
            }
        },
        isLastInList () {
            return this.index === this.items.length - 1;
        },
        dropPosition () {
            if (this.elmRect && (this.offsetX || this.offsetY)) {
                if(this.isCompactView){
                    if(this.isLastInList && this.offsetY >= this.elmRect.height - 8 && this.offsetY <= this.elmRect.height){
                        return 'after';
                    } else if((this.offsetY >= 1 && this.offsetY <= 8) || (!this.isLastInList && this.isLink)) {
                        return 'before';
                    } else if(!this.isLink){
                        return 'into';
                    }
                } else {
                    if(this.isLastInList && this.offsetX >= this.elmRect.width - 25 && this.offsetX <= this.elmRect.width){
                        return 'after';
                    } else if((this.offsetX >= 1 && this.offsetX <= 25) || (!this.isLastInList && this.isLink)) {
                        return 'before';
                    } else if(!this.isLink){
                        return 'into';
                    }
                }
            }
            return '';
        },
        colors () {
            return this.$store.state.colorsCache.items;
        }
    },
    mounted () {
        this.$root.$on('browser-started-rename', (bookmarkId) => {
            if(this.rename && this.id !== bookmarkId){
                this.renameFinish();
            }
        });
        this.$root.$on('show-context-menu-for-id', (bookmarkId) => {
            if(this.model.id === bookmarkId){
                let rect = this.$el.getBoundingClientRect();
                this.showContextMenu({pageX: rect.left, pageY: rect.top, initiatedWithKeyboard: true});
            }
        });
        this.$root.$on('bookmarkCreated', (bookmark) => {
            if(this.model.id === bookmark.parentId){
                this.playAnim();
            }
        });
        this.$root.$on('bookmarkMoved', (id, info, bookmark) => {
            if(this.model.id === info.parentId){
                this.playAnim();
            }
        });
        this.$watch('browser.focusOnId', (newVal, oldVal) => {
            if(this.isFocused){
                this.$nextTick(()=>{
                    this.$el.scrollIntoViewIfNeeded(); // https://developer.mozilla.org/en-US/docs/Web/API/Element/scrollIntoViewIfNeeded
                });
            }
        });
        this.$watch('browser.focusOnIndex', (newVal, oldVal) => {
            if(this.isKeyboardFocused){
                this.$el.scrollIntoViewIfNeeded();
                // this.$el.scrollIntoView({
                 //    block: 'center'
                // }); // TODO
            }
        });
        this.$watch('model.browserForceRename', (newVal, oldVal) => {
            if(newVal){
                this.$el.scrollIntoViewIfNeeded();
                this.renameStart();
            }
        });
        this.$watch('$store.state.columns', (newVal, oldVal) => {
            this.calcWidth();
        }, {
            deep: true
        });
        this.$watch('browser.view', (newVal, oldVal) => {
            this.calcWidth();
        });
        if(this.isFocused || this.model.browserForceRename){
            this.$nextTick(()=>{
                this.$el.scrollIntoViewIfNeeded();
            });
        }
        if(this.model.browserForceRename){
            this.renameStart();
        }
        if(this.index === 0){
            window.addEventListener('resize', this.calcWidth);
        }
    },
    updated () {
        this.calcWidth();
    },
    beforeDestroy () {
        if(this.index === 0){
            window.removeEventListener('resize', this.calcWidth);
        }
    },
    methods: {
        playAnim () {
            if(!this.playFolderChangedAnim){
                this.playFolderChangedAnim = true;
            }
            this.animTimer = window.setTimeout(() => {
                window.clearTimeout(this.animTimer);
                this.playFolderChangedAnim = false;
            }, 1000); // TODO old anim
        },
        calcWidth () {
            this.$nextTick( () => {
                this.$emit('widthReport', {width: this.$el.clientWidth});
            });
        },
        itemDragStart (e) {
            // e.dataTransfer.setDragImage(createDragItem(this.model.url, this.model.title, !this.isLink), -20, 0);
            this.$emit('dragStart', {item: this.model, event: e});
            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                browserName: this.browser.name,
                index: undefined
            });
        },
        itemDragEnter (e) {
            this.elmRect = e.target.getBoundingClientRect();
            if(e.target.classList.contains('browser-item')){
                this.noChildEvents = true;
            }
        },
        itemDragOver (e) {
            // this.dragInProgress = true;
            if(!this.dragItem.startEvent || this.dragItem.startEvent.target !== e.target){
                if(this.isCompactView){
                    this.offsetY = e.offsetY;
                } else {
                    this.offsetX = e.offsetX;
                }
            }
        },
        itemDragLeave (e) {
            this.offsetX = 0;
            this.offsetY = 0;
            if(e.target.classList.contains('browser-item')){
                this.noChildEvents = false;
            }
            // this.dragInProgress = false;
            // this.dragState = '';
        },
        itemDrop (e) {
            if(!this.browser.search.text){
                let index = this.dropPosition === 'into' ? undefined :
                    this.dropPosition === 'after' ? this.model.index + 1 : this.model.index;
                let parentId = this.dropPosition === 'into' ? this.model.id : this.model.parentId;
                let items = Array.isArray(this.dragItem.data) ? this.dragItem.data : [this.dragItem.data];

                switch (this.dragItem.type) {
                    case types.DRAG_ITEM_TYPE_BOOKMARK:
                    case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                        for(let i = 0; i < items.length; i++){
                            if(items[i].id !== parentId){
                                brwsr.moveBookmark(items[i].id, {
                                    index: index,
                                    parentId: parentId
                                });
                            }
                        }
                        break;
                    case types.DRAG_ITEM_TYPE_TAB:
                    case types.DRAG_ITEM_TYPE_RECENT:
                    case types.DRAG_ITEM_TYPE_TOP_SITE:
                        for(let i = 0; i < items.length; i++){
                            brwsr.createBookmark({
                                title: items[i].title,
                                url: items[i].url,
                                parentId: parentId,
                                index: index
                            });
                        }
                        break;
                    case types.DRAG_ITEM_TYPE_BROWSER_WINDOW:
                        const currentDate = new Date();
                        const tabs = this.dragItem.data.tabs;
                        brwsr.createBookmark({
                            title: 'Window '+ currentDate.toLocaleString(),
                            index: index,
                            parentId: parentId
                        }).then((folder) => {
                            tabs.forEach((tab, i) => {
                                window.setTimeout(() => {
                                    brwsr.createBookmark({
                                        title: tab.title,
                                        url: tab.url,
                                        parentId: folder.id
                                    });
                                }, i * 50);
                            });
                        });
                        break;
                }
            }
            this.$emit('resetSelectedItems');
            this.$store.commit(types.RESET_DRAG_ITEM );
            this.noChildEvents = false;
            this.offsetX = 0;
            this.offsetY = 0;
            // this.dragInProgress = false;
            // this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
            //     browserName: this.browser.name,
            //     index: this.index
            // });
            // this.dragState = '';
        },
        onMouseDown (e) {
            if(!this.rename){
                if(e.button === 1){
                    if(this.isLink){
                        this.openInBackground();
                    } else {
                        this.$store.commit(types.ADD_NEW_BB, {folder: this.model.id});
                    }
                    e.preventDefault();
                }
            } else {
                this.$store.commit(types.SET_ACTIVE_SPACE, '');
                e.stopPropagation();
            }
        },
        openInBackground () {
            brwsr.createTab({url: this.model.url, active: false});
        },
        select (e) {
            this.$emit('selectMe', {item: this.model, mouseEvent: e});
            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                browserName: this.browser.name,
                index: undefined
            });
            e.preventDefault();
            e.stopPropagation();
        },
        open (e) {
            if(e.ctrlKey || e.shiftKey) {
                this.select(e);
            } else if(this.isLink && e.button !== 1){
                window.location.replace(this.model.url);
            } else {
                this.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
                    browserName: this.browser.name,
                    folderId: this.model.id
                });
                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                    browserName: this.browser.name,
                    index: undefined
                });
            }
        },
        renameStart () {
            this.$set(this.model, 'rename', true);
            this.$set(this.model, 'oldTitle', this.model.title);
            this.$nextTick( () => {
                this.renameInput.focus();
                this.renameInput.select();
            });
            window.addEventListener('click', this.renameFinish);
            window.setTimeout(()=>{
                this.$store.commit(types.SET_ACTIVE_SPACE, '');
            }, 200); // TODO -_-
        },
        renameFinish (e) {
            if(this.rename){
                this.model.rename = false;
                this.model.browserForceRename = false;
                brwsr.updateBookmark(this.id, {title: this.model.title});
                window.removeEventListener('click', this.renameFinish);
                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                    browserName: this.browser.name,
                    index: this.model.index
                });
                this.$store.commit(types.SET_ACTIVE_SPACE, this.browser.spaceName);
            }
        },
        renameCancel () {
            if(this.rename){
                this.model.rename = false;
                this.model.browserForceRename = false;
                this.model.title = this.model.oldTitle;
                window.removeEventListener('click', this.renameFinish);
                this.$store.commit(types.SET_ACTIVE_SPACE, this.browser.spaceName);
            }
        },
        showContextMenu (e) {
            // Reset selection on other items
            if(this.selectedItems.length && !this.isSelected){
                this.$emit('resetSelectedItems');
            }

            let items = [];
            if(this.isLink){
                items.push({
                    title: 'Open in new tab',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$emit('openMe', {
                            bookmark: item.vm.model
                        });
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Open in background',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$emit('openMe', {
                            bookmark: item.vm.model,
                            props: {
                                active: false
                            }
                        });
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Open in new window',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$emit('openMe', {
                            bookmark: item.vm.model,
                            props: {
                                state: 'maximized'
                            },
                            newWindow: true
                        });
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Open in incognito window',
                    vm: this,
                    hr: true,
                    clickFn: function (item, menu) {
                        item.vm.$emit('openMe', {
                            bookmark: item.vm.model,
                            props: {
                                incognito: true,
                                state: 'maximized'
                            },
                            newWindow: true
                        });
                        menu.hide();
                    }
                });
            }
            if(!this.isLink){
                items.push({
                    title: 'Open in Folder bar',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$store.commit(types.ADD_NEW_BB, {folder: item.vm.model.id});
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Create Window',
                    vm: this,
                    clickFn: function (item, menu) {
                        brwsr.getBookmarkChildren(item.vm.model.id).then((bookmarks) => {
                            if(bookmarks.length) {
                                let urls = [];
                                for(let i=0; i < bookmarks.length; i++ ){
                                    if(bookmarks[i].url){
                                        urls.push(bookmarks[i].url);
                                    }
                                }
                                brwsr.createWindow({
                                    url: urls,
                                    state: 'maximized'
                                });
                            }
                        });
                        menu.hide();
                    }
                });
            }
            items.push({
                title: 'Cut',
                vm: this,
                clickFn: function (item, menu) {
                    // item.vm.$store.commit(types.ADD_TO_CLIPBOARD, item.vm.id);
                    item.vm.$emit('cutMe', item.vm.model);
                    menu.hide();
                }
            });
            if(!this.isLink){
                items.push({
                    title: 'Paste',
                    vm: this,
                    inactive: this.clipboard.length === 0,
                    clickFn: function (item, menu) {
                        let clipboard = item.vm.clipboard;
                        if(item.vm.clipboard.length > 0){
                            for(let i=0; i< clipboard.length; i++){
                                brwsr.moveBookmark(clipboard[i], {
                                    parentId: item.vm.id
                                }).then((bookmark) => {
                                    item.vm.$store.commit(types.REMOVE_FROM_CLIPBOARD, bookmark.id); // TODO ?
                                });
                            }
                            menu.hide();
                        }
                    }
                });
            }
            if(!this.isLink || !this.isTileView){
                items.push({
                    title: 'Rename',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$root.$emit('browser-started-rename', item.vm.id);
                        item.vm.renameStart();
                        menu.hide();
                    }
                });
            }
            if(this.isLink){
                items.push({
                    title: 'Edit',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$root.$emit('dialog-edit-bookmark', item.vm.id);
                        menu.hide();
                    }
                });
            }
            items.push({
                title: 'Delete',
                vm: this,
                hr: !this.isLink,
                confirm: false,
                inactive: this.isQuickStartFolder,
                clickFn (item, menu) {
                    if(!item.confirm){
                        item.confirm = true;
                    } else {
                        item.vm.$emit('deleteMe', item.vm.model);
                        menu.hide();
                    }
                }
            });
            if(!this.isLink){
                items.push({
                    title: 'Set as Quick Start',
                    vm: this,
                    clickFn: function (item, menu) {
                        const bookmark = item.vm.model;
                        item.vm.$store.commit(types.SET_QUICK_START_FOLDER, bookmark);
                        menu.hide();
                    }
                });
            }
            if(this.isLink){
                items.push({
                    title: 'Copy link to clipboard',
                    vm: this,
                    clickFn: function (item, menu) {
                        navigator.clipboard.writeText(item.vm.model.url);
                        menu.hide();
                    }
                });
            }
            if(this.isLink && this.browser.currentFolderId !== this.$store.state.quickStartFolder.id){
                items.push({
                    title: 'Add to Quick Start',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$emit('addMeToQuickStart', item.vm.model);
                        menu.hide();
                    }
                });
            }
            this.$root.$emit('show-context-menu', {
                left: e.pageX,
                top: e.pageY,
                keyboard: e.initiatedWithKeyboard,
                items: items
            });
            if(e.hasOwnProperty('stopPropagation')) e.stopPropagation();
            if(e.hasOwnProperty('preventDefault')) e.preventDefault();
        },
        onImgLoad (e) {
            if(!this.isTilePicOverridden && !this.isTileView && !this.isCompactView && !this.isIconOverridden){
                if(this.colors[this.domain]){
                    this.mainColor = this.colors[this.domain];
                } else {
                    this.mainColor = colorHelper.getMainColor(this.imgFavicon);
                    this.$store.commit(types.UPDATE_COLOR_IN_CACHE, {
                        domain: this.domain,
                        color: this.mainColor
                    });
                }
            }
        },
    }
}