import * as types from "../../store/mutation-types"
import brwsr from '../../services/browser'

export default {
    data () {
        return {
            display: false,
            prevActiveSpace: '',
            item: {
                title: '',
                url: ''
            },
            tmp: {
                iconUrl: '',
                tilePicUrl: '',
                overrideForDomain: false
            },
            override: {
                iconUrl: '',
                tilePicUrl: '',
                tileTitle: '',
                iconHostname: ''
            }
        }
    },
    computed: {
        favicon () {
            if(this.override.iconUrl){
                return this.override.iconUrl;
            } else {
                // return 'chrome://favicon/size/16@2x/' + this.item.url;
                return brwsr.FAVICON_URL + this.item.url;
            }
        },
        isIconOverridden () {
            return this.override.iconUrl;
        },
        title () {
            return this.item.title
        },
        tileTitle () {
            if(this.override.tileTitle){
                return this.override.tileTitle;
            } else {
                let link = document.createElement('a');
                link.href = this.item.url;
                let host = link.hostname.indexOf('www.') === -1 ? link.hostname : link.hostname.substring(4);
                // return host.indexOf('.') === -1 ? host : host.substring(0, host.indexOf('.')); // TODO
                let s = host.indexOf('.') === -1 ? host : host.substring(0, host.indexOf('.')); // TODO
                return s.charAt(0).toUpperCase() +	s.slice(1);
            }
        },
        hostname () {
            let link = document.createElement('a');
            link.href = this.item.url;
            return link.hostname;
        },
        iconOverrides () {
            return this.$store.state.iconOverrides;
        },
        tilePicOverrides () {
            return this.$store.state.tilePicOverrides;
        },
        tileTitleOverrides () {
            return this.$store.state.tileTitleOverrides;
        },
    },
    created () {
        this.$root.$on('dialog-create-bookmark', (parentId) => {
            this.item.parentId = parentId;
            this.show();
        });
        this.$root.$on('dialog-edit-bookmark', (bookmarkId) => {
            brwsr.getBookmark(String(bookmarkId)).then((bookmarks) => {
                if(bookmarks.length > 0){
                    this.item = bookmarks[0];
                    this.show();
                }
            });
        });
        this.$watch('tmp.iconUrl', (iconUrl) => {
            this.override.iconUrl = this.tmp.iconUrl;
        });
        this.$watch('tmp.tilePicUrl', (tilePicUrl) => {
            this.override.tilePicUrl = this.tmp.tilePicUrl;
        });
        this.$watch('tmp.overrideForDomain', (tilePicUrl) => {
            this.override.iconHostname = this.tmp.overrideForDomain ? this.hostname : '';
        });
    },
    methods: {
        show () {
            if(this.iconOverrides[this.hostname]){
                this.override.iconUrl = this.iconOverrides[this.hostname];
                this.tmp.overrideForDomain = true;
            }
            if(this.iconOverrides[this.item.url]){
                this.override.iconUrl = this.iconOverrides[this.item.url];
                this.tmp.overrideForDomain = false;
            }
            if(this.tilePicOverrides[this.item.url]){
                this.override.tilePicUrl = this.tilePicOverrides[this.item.url];
            }
            if(this.tileTitleOverrides[this.item.url]){
                this.override.tileTitle = this.tileTitleOverrides[this.item.url];
            }
            // load in tmp (base64 url freeze)
            if(this.override.iconUrl && this.override.iconUrl.indexOf('data:image/') !== 0){
                this.tmp.iconUrl = this.override.iconUrl;
            }
            if(this.override.tilePicUrl && this.override.tilePicUrl.indexOf('data:image/') !== 0){
                this.tmp.tilePicUrl = this.override.tilePicUrl;
            }

            this.display = true;
            window.document.addEventListener('keydown', this.onKey);
            this.prevActiveSpace = this.$store.state.activeSpace;
            this.$nextTick(()=>{
                this.$store.commit(types.SET_ACTIVE_SPACE, '');
            });
        },
        hide () {
            this.item = {};
            this.override = {
                iconUrl: '',
                tilePicUrl: '',
                tileTitle: '',
                iconHostname: ''
            };
            this.tmp = {
                iconUrl: '',
                tilePicUrl: '',
                overrideForDomain: false
            };
            this.display = false;
            window.document.removeEventListener('keydown', this.onKey);
            this.$store.commit(types.SET_ACTIVE_SPACE, this.prevActiveSpace);
        },
        onKey (e) {
            if(e.code === 'Escape'){
                this.hide();
            }
        },
        save () {
            if(this.item.id > 0){
            	brwsr.updateBookmark(this.item.id, {
					title: this.item.title,
					url: this.item.url
				}).then(() => {
					this.hide();
				});
            } else {
                if(this.item.url.length > 0){
                	brwsr.createBookmark({
						title: this.item.title,
						url: this.item.url,
						parentId: this.item.parentId
					}).then(() => {
						this.hide();
					});
                }
            }
            this.$store.commit(types.SET_OVERRIDE_ITEM, {override: this.override, item: this.item});
        },
        loadImage (type, e) {
            let file = new FileReader();
            file.addEventListener('load', () => {
                if(e.target.files[0].type.split('/')[0] === 'image'){
                    let canvas = document.createElement('canvas'),
                        ctx = canvas.getContext('2d'),
                        img = new Image();

                    img.crossOrigin = "Anonymous"; // https://developer.mozilla.org/en-US/docs/Web/HTML/CORS_enabled_image
                    img.onload = () => {
                        if(type === 'icon'){
                            canvas.width = 64;
                            canvas.height = 64;
                            ctx.imageSmoothingQuality = 'high';
                            ctx.imageSmoothingEnabled = true;
                            ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
                            this.override.iconUrl = canvas.toDataURL();
                        } else if(type === 'tile') {
                            canvas.width = 256;
                            canvas.height = canvas.width * img.height / img.width;
                            ctx.imageSmoothingQuality = 'high';
                            ctx.imageSmoothingEnabled = true;
                            ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
                            this.override.tilePicUrl = canvas.toDataURL();
                        }
                    };
                    img.src = file.result;
                }
            }, false);
            file.readAsDataURL(e.target.files[0]);
        },
        removeTilePic () {
            this.override.tilePicUrl = '';
            this.tmp.tilePicUrl = '';
        },
        removeIcon () {
            this.override.iconUrl = '';
            this.tmp.iconUrl = '';
        },
        openManager () {
            this.$store.commit(types.OPEN_OVERRIDES_MANAGER);
        }
    }
}