import * as types from '../../store/mutation-types'
import brwsr from "../../services/browser";

export default {
    props: [
        'icons',
        'model',
        'index',
        'currentIndex',
        'focusOnParent'
    ],
    data () {
        return {
            folderName: ''
        }
    },
    computed: {
        icon () {
            if(!this.isFolder){
                return this.icons[this.model.url] || this.icons[this.domain] || brwsr.FAVICON_URL + this.model.url
            }
            return false;
        },
        domain () {
            if(!this.isFolder){
                let link = document.createElement('a');
                link.href = this.model.url;
                return link.hostname;
            }
            return '';
        },
        isFolder () {
            return brwsr.isBookmarkFolder(this.model);
        },
        isLink () {
            return !this.isFolder;
        },
        selected () {
            return this.index === this.currentIndex;
        },
        classes () {
            return {
                'search-list-item-active': this.selected,
                'search-bar-item-folder-active': this.focusOnParent && this.selected
            }
        }
    },
    methods: {
        open () {
            this.$emit('openFoundItem', this.model);
        },
        openInBackground () {
            if(this.model.url){
                brwsr.createTab({url: this.model.url, active: false});
            }
        },
        openFolder () {
            this.$emit('openFoundItemsFolder', this.model.parentId, this.model.id);
        },
        showContextMenu (e) {
            let items = [];
            if(this.isLink){
                items.push({
                    title: 'Open in new tab',
                    vm: this,
                    clickFn: function (item, menu) {
                        brwsr.createTab({url: item.vm.model.url});
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Open in background',
                    vm: this,
                    clickFn: function (item, menu) {
                        brwsr.createTab({url: item.vm.model.url, active: false});
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Open in new window',
                    vm: this,
                    clickFn: function (item, menu) {
                        brwsr.createWindow({
                            url: item.vm.model.url,
                            state: 'maximized'
                        });
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Open in incognito window',
                    vm: this,
                    hr: true,
                    clickFn: function (item, menu) {
                        brwsr.createWindow({
                            url: item.vm.model.url,
                            incognito: true,
                            state: 'maximized'
                        });
                        menu.hide();
                    }
                });
            }
            if(!this.isLink){
                items.push({
                    title: 'Open in Folder bar',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$store.commit(types.ADD_NEW_BB, {
                            folder: item.vm.model.id
                        });
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Create Window',
                    vm: this,
                    clickFn: function (item, menu) {
                        brwsr.getBookmarkChildren(item.vm.model.id).then((bookmarks) => {
                            if(bookmarks.length) {
                                let urls = [];
                                for(let i=0; i < bookmarks.length; i++ ){
                                    if(bookmarks[i].url){
                                        urls.push(bookmarks[i].url);
                                    }
                                }
                                brwsr.createWindow({
                                    url: urls,
                                    state: 'maximized'
                                });
                            }
                        });
                        menu.hide();
                    }
                });
            }
            if(this.isLink){
                items.push({
                    title: 'Edit',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$root.$emit('dialog-edit-bookmark', item.vm.model.id);
                        menu.hide();
                        item.vm.$emit('resetInput');
                    }
                });
            }
            if(this.isLink){
                items.push({
                    title: 'Copy link to clipboard',
                    vm: this,
                    clickFn: function (item, menu) {
                        navigator.clipboard.writeText(item.vm.model.url);
                        menu.hide();
                    }
                });
            }
            this.$root.$emit('show-context-menu', {
                left: e.pageX,
                top: e.pageY,
                keyboard: e.initiatedWithKeyboard,
                items: items
            });
            if(e.hasOwnProperty('stopPropagation')) e.stopPropagation();
            if(e.hasOwnProperty('preventDefault')) e.preventDefault();
        }
    }
}