import Vue from 'vue'
import * as types from './mutation-types'
import brwsr from '../services/browser'
import colorHelper from "../helpers/color";

export const restoreDecor = ({commit, state}) => {
    if(window.localStorage.getItem('migratedTo1.0.0')){
        brwsr.getStorageData('local', [
            types.STORAGE_KEY_DECOR
        ]).then((result) => {
            if(result.hasOwnProperty(types.STORAGE_KEY_DECOR)){
                const d = JSON.parse(result[types.STORAGE_KEY_DECOR]);
                // Vue.set(state, 'decor', JSON.parse(result[types.STORAGE_KEY_DECOR])); // TODO
                state.decor.adaptationToBackgroundPic = d.adaptationToBackgroundPic;
                state.decor.theme.name = d.theme.name;
                state.decor.background.autoChange = d.background.autoChange;
                state.decor.background.autoChangeModify = d.background.autoChangeModify;
                state.decor.background.id = d.background.id;
                state.decor.background.url = d.background.url;
                state.decor.background.author = d.background.author;
                state.decor.background.type = d.background.type;
                state.decor.background.opacity = d.background.opacity;
                state.decor.background.gamma = d.background.gamma;
                state.decor.background.color = d.background.color;
                state.decor.background.effect.blend = d.background.effect.blend;
                state.decor.background.effect.color = d.background.effect.color;
                state.decor.background.sets.unsplash.photos = d.background.sets.unsplash.photos;
                state.decor.background.sets.unsplash.modify = d.background.sets.unsplash.modify;
                state.decor.text.color = d.text.color;
                state.decor.text.compactViewLineSpacingSize = d.text.compactViewLineSpacingSize || 0;
                state.decor.tile.backgroundColor = d.tile.backgroundColor;
                state.decor.tile.borderColor = d.tile.borderColor;

                if(!window.localStorage.getItem('migratedTo1.0.1')){
                    // TODO migration from 1.0.0 to 1.0.1, del
                    if(state.decor.tile.backgroundColor.toString() === '255,255,255,0.5' || state.decor.tile.backgroundColor.toString() === '71,71,71,0.6'){
                        commit(types.SET_DECOR_PROP, {prop: 'tile.backgroundColor', value: []});
                    }
                    if(state.decor.tile.borderColor.toString() === '0,0,0,0.15' || state.decor.tile.borderColor.toString() === '0,0,0,0.3'){
                        commit(types.SET_DECOR_PROP, {prop: 'tile.borderColor', value: []});
                    }
                    if(state.decor.text.color.toString() === '0,0,0,1' || state.decor.text.color.toString() === '255,255,255,0.9'){
                        commit(types.SET_DECOR_PROP, {prop: 'text.color', value: []});
                    }
                    window.localStorage.setItem('migratedTo1.0.1', '1')
                }
            }
        });
    } else {
        // TODO migration from 0.7.4 to 1.0.0, del
        brwsr.getStorageData('local', [
            types.STORAGE_KEY_THEME,
            types.STORAGE_KEY_CUSTOM_DECOR,
            types.STORAGE_KEY_THEME_AUTO_SELECT,
            types.STORAGE_KEY_BACKGROUND,
            types.STORAGE_KEY_BACKGROUND_COLOR
        ]).then((result) => {
            if(result.hasOwnProperty(types.STORAGE_KEY_BACKGROUND)){
                commit(types.SET_BACKGROUND, JSON.parse(result[types.STORAGE_KEY_BACKGROUND]));
            }
            if(result.hasOwnProperty(types.STORAGE_KEY_THEME)){
                commit(types.SET_DECOR_PROP, {
                    prop: 'theme.name',
                    value: result[types.STORAGE_KEY_THEME] === 'light' ? 'light' : 'dark'
                });
            }
            if(result.hasOwnProperty(types.STORAGE_KEY_BACKGROUND_COLOR)){
                commit(types.SET_DECOR_PROP, {
                    prop: 'background.color',
                    value: JSON.parse(result[types.STORAGE_KEY_BACKGROUND_COLOR])
                });
            }
            if(result.hasOwnProperty(types.STORAGE_KEY_CUSTOM_DECOR)){
                const oldDecor = JSON.parse(result[types.STORAGE_KEY_CUSTOM_DECOR]);
                if(oldDecor.hasOwnProperty('bgBlendModeIntensity')){
                    commit(types.SET_DECOR_PROP, {
                        prop: 'background.opacity',
                        value: oldDecor.bgBlendModeIntensity
                    });
                }
                if(oldDecor.hasOwnProperty('tileBgColor') &&
                    oldDecor.tileBgColor.toString() !== '255,255,255,0.5' &&
                    oldDecor.tileBgColor.toString() !== '71,71,71,0.6') {
                    commit(types.SET_DECOR_PROP, {
                        prop: 'tile.backgroundColor',
                        value: oldDecor.tileBgColor
                    });
                }
                if(oldDecor.hasOwnProperty('tileBorderColor') &&
                    oldDecor.tileBorderColor.toString() !== '0,0,0,0.15' &&
                    oldDecor.tileBorderColor.toString() !== '0,0,0,0.3') {
                    commit(types.SET_DECOR_PROP, {
                        prop: 'tile.borderColor',
                        value: oldDecor.tileBorderColor
                    });
                }
                if(oldDecor.hasOwnProperty('textColor') &&
                    oldDecor.textColor.toString() !== '0,0,0,1' &&
                    oldDecor.textColor.toString() !== '255,255,255,0.9') {
                    commit(types.SET_DECOR_PROP, {
                        prop: 'text.color',
                        value: oldDecor.textColor
                    });
                }
            }
            // if(result.hasOwnProperty(types.STORAGE_KEY_THEME_AUTO_SELECT)){
            //     commit(types.SET_DECOR_PROP, {
            //         prop: 'adaptationToBackgroundPic',
            //         value: result[types.STORAGE_KEY_THEME_AUTO_SELECT]
            //     });
            // }
            brwsr.removeStorageData('local', [
                types.STORAGE_KEY_THEME,
                types.STORAGE_KEY_CUSTOM_DECOR,
                types.STORAGE_KEY_THEME_AUTO_SELECT,
                types.STORAGE_KEY_BACKGROUND,
                types.STORAGE_KEY_BACKGROUND_COLOR
            ]).then(() => {
                window.localStorage.setItem('migratedTo1.0.0', '1')
            });
        });
    }
};

export const restoreOverrides = ({commit, state}) => {
    brwsr.getStorageData('local', [
        types.STORAGE_ICON_OVERRIDES,
        types.STORAGE_TILE_PIC_OVERRIDES,
        types.STORAGE_TILE_TITLE_OVERRIDES
    ]).then((result) => {
        if(result.hasOwnProperty(types.STORAGE_ICON_OVERRIDES)){
            state.iconOverrides = JSON.parse(result[types.STORAGE_ICON_OVERRIDES]);
        }
        if(result.hasOwnProperty(types.STORAGE_TILE_PIC_OVERRIDES)){
            state.tilePicOverrides = JSON.parse(result[types.STORAGE_TILE_PIC_OVERRIDES]);
        }
        if(result.hasOwnProperty(types.STORAGE_TILE_TITLE_OVERRIDES)){
            state.tileTitleOverrides = JSON.parse(result[types.STORAGE_TILE_TITLE_OVERRIDES]);
        }
    });
};

export const restoreHiddenItems = ({commit, state}) => {
    return new Promise((resolve, reject) => {
        brwsr.getStorageData('sync', types.STORAGE_KEY_RECENT_HIDDEN_ITEMS).then((result) => {
            if(result.hasOwnProperty(types.STORAGE_KEY_RECENT_HIDDEN_ITEMS)){
                state.recentHiddenItems = result[types.STORAGE_KEY_RECENT_HIDDEN_ITEMS];
            }
            resolve();
        });
    })
};

let timeoutId;
export const saveCheckedHiddenRecentItems = ({commit, state}, items) => {
    window.clearTimeout(timeoutId);
    timeoutId = window.setTimeout( () => {
        brwsr.setStorageData('sync', {
            [types.STORAGE_KEY_RECENT_HIDDEN_ITEMS]: items
        });
    }, 2000);
};

