import Vue from 'vue'
import * as types from './mutation-types'
import bgImages from './_images'
import bgPatterns from './_patterns'
import brwsr from '../services/browser'

export const state = {
    whatIsNewAvailable: false,
    debug: false,
    activeSpace: '',
    clipboard: [],
    quickStartFolder: {},
    openedFolders: [brwsr.BOOKMARKS_ROOT_FOLDER_ID],
    foldersConfig: {},
    recentHiddenItems: [],
    dragItem: {}, // { type: string, startEvent: drag start event {}, data: {} || [] }
    settings: {
        newSettingsAvailable: false,
        version: 8,
        load: true,
        display: false
    },
    showTopBar: true,
    topBarHeight: 80,
    toggleTopBarHeight: 10,
    columns: {
        left: {
            id: 'left',
            position: 'left',
            width: 320,
            display: true,
            activeMenu: 'folders',
        },
        right: {
            id: 'right',
            position: 'right',
            width: 320,
            display: true,
            activeMenu: 'tabs',
        }
    },
    previousBrowserName: 'b1',
    activeBrowser: 'b1',
    searchEngineId: 'rka0w',
    browsers: [
        {
            name: 'b1',
            title: 'Quick Start',
            permanent: true,
            spaceName: 'space_b1',
            topBar: false,
            topLevelId: '',
            currentFolderId: '',
            focusOnId: '',
            focusOnIndex: undefined,
            search: {
                text: '',
                items: []
            },
            view: 'tile',  // grid, list, compact, tile
            quickStart: true
        },{
            name: 'b2',
            title: 'Bookmarks',
            permanent: true,
            spaceName: 'space_b2',
            topBar: true,
            topLevelId: brwsr.BOOKMARKS_ROOT_FOLDER_ID,
            currentFolderId: brwsr.BOOKMARKS_ROOT_FOLDER_ID,
            focusOnId: '',
            focusOnIndex: undefined,
            search: {
                text: '',
                items: []
            },
            view: 'grid'
        }
    ],
    decor: {
        adaptationToBackgroundPic: true,
        theme: {
            name: 'light', // 'light', 'dark'
        },
        background: {
            autoChange: false,
            autoChangeModify: 0,
            id: '',
            url: '',
            author: null,
            type: '', // need?
            fit: 'cover', // pattern? need?
            opacity: 0.5,
            gamma: -1, // for pics, pattern
            color: [],
            effect: {
                blend: '',
                color: [],
                // color: [255,255,255, 0.4],
            },
            sets: {
                pictures: bgImages,
                patterns: bgPatterns,
                unsplash: {
                    photos: [],
                    modify: 0
                }
            }
        },
        text: {
            color: [],
            compactViewLineSpacingSize: 0
        },
        tile: {
            backgroundColor: [],
            borderColor: []
        },
        blendModes: [
            'lighten',
            'multiply',
            'overlay',
            'screen',
            'darken',
            'color',
            'color-dodge',
            'color-burn',
            'saturation',
            'luminosity',
            'difference',
            'exclusion',
            'hard-light',
            'hue'
        ],
    },
    tilePicOverrides: {},
    tileTitleOverrides: {},
    iconOverrides: {},
    showOverridesManager: false,
    colorsCache: {
        items: {},
        lastReset: 0 // not used
    },
    config: {
        useVisibleOptionForHideColumnTopBar: false,
    }
};

export const mutations = {
    [types.RESTORE_CONFIG] (state) {
        if(window.localStorage.getItem(types.STORAGE_KEY_CONFIG) !== null){
            Vue.set(state, 'config', JSON.parse(window.localStorage.getItem(types.STORAGE_KEY_CONFIG)));
        } // TODO
    },
    [types.CHANGE_CONFIG] (state, {prop, value}) {
        if(state.config.hasOwnProperty(prop)){
            state.config[prop] = value;
        }
    },

    [types.RESTORE_FOLDERS_CONFIG] (state) {
        if(window.localStorage.getItem(types.STORAGE_KEY_FOLDERS_CONFIG) !== null){
            Vue.set(state, 'foldersConfig', JSON.parse(window.localStorage.getItem(types.STORAGE_KEY_FOLDERS_CONFIG)));
        }
    },
    [types.SET_FOLDERS_CONFIG] (state, {folderId, prop, value}) {
        if(!state.foldersConfig[folderId]){
            Vue.set(state.foldersConfig, folderId, {});
        }
        Vue.set(state.foldersConfig[folderId], prop, value);
    },
    [types.DELETE_FOLDERS_CONFIG_PROP] (state, {folderId, prop, value}) {
        if(state.foldersConfig[folderId] && state.foldersConfig[folderId].hasOwnProperty(prop)){
            Vue.delete(state.foldersConfig[folderId], prop);
        }
    },
    [types.DELETE_FOLDERS_CONFIG] (state, folderId) {
        if(state.foldersConfig[folderId]){
            Vue.delete(state.foldersConfig, folderId);
        }
    },

    [types.SET_ACTIVE_BOOKMARKS_BROWSER] (state, browserName) {
        state.previousBrowserName = state.activeBrowser;
        state.activeBrowser = browserName;
    },
    [types.RESTORE_ACTIVE_BOOKMARKS_BROWSER] (state) {
        if(window.localStorage.getItem(types.STORAGE_KEY_ACTIVE_BROWSER) !== null){
            state.activeBrowser = window.localStorage.getItem(types.STORAGE_KEY_ACTIVE_BROWSER);
        }
    },

    [types.SET_QUICK_START_FOLDER] (state, bookmark) {
        Vue.set(state, 'quickStartFolder', bookmark); // TODO ?
        const browser = state.browsers.find(b => b.quickStart === true);
        if(browser){
            browser.topLevelId = bookmark.id;
            browser.currentFolderId = bookmark.id;
        }
    },

    [types.SET_DRAG_ITEM] (state, data) {
        Vue.set(state, 'dragItem', data);
    },
    [types.RESET_DRAG_ITEM] (state) {
        Vue.set(state, 'dragItem', {});
    },
    [types.UPDATE_DRAG_ITEM] (state, {prop, value}) {
        state.dragItem[prop] = value;
    },

    [types.BB_SET_SEARCH_RESULT] (state, {browserName, text, items}) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            browser.search.text = text;
            browser.search.items = items;
        }
    },
    [types.BB_RESET_SEARCH] (state, browserName) {
        let browser = state.browsers.find( b => b.name === browserName );
        browser.focusOnId = '';
        browser.search.text = '';
        browser.search.items = [];
    },
    [types.CHANGE_SEARCH_ENGINE] (state, {id}) {
        state.searchEngineId = id;
    },
    [types.RESTORE_SEARCH_ENGINE] (state) {
        if(window.localStorage.getItem(types.STORAGE_SEARCH_ENGINE_ID) !== null){
            state.searchEngineId = window.localStorage.getItem(types.STORAGE_SEARCH_ENGINE_ID);
        }
    },

    [types.BB_CHANGE_CURRENT_FOLDER] (state, {browserName, folderId}) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            browser.currentFolderId = folderId;
            sendBBchangeMessage();
        }
    },
    [types.BB_CHANGE_VIEW] (state, {browserName, view}) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            browser.view = view;
        }
    },
    [types.BB_SET_FOCUS_ON_ITEM] (state, {browserName, bookmarkId}) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            browser.focusOnId = bookmarkId;
        }
    },
    [types.BB_SET_FOCUS_ON_INDEX] (state, {browserName, index}) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            Vue.set(browser, 'focusOnIndex', index);
        }
        // browser.focusOnIndex = index;
    },
    [types.BB_SET_TOP_LEVEL_ID] (state, {browserName, bookmarkId}) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            browser.topLevelId = bookmarkId;
        }
    },
    [types.BB_SET_TITLE] (state, {browserName, title}) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            browser.title = title;
        }
    },
    [types.ADD_NEW_BB] (state, {searchResult, folder, toIndex}) {
        let name = Math.random().toString(36).substring(7); // https://stackoverflow.com/questions/1349404/generate-random-string-characters-in-javascript
        while (state.browsers.findIndex(b => b.name === name) !== -1){
            name = Math.random().toString(36).substring(7);
        }
        let bookmarkBrowser = {
            name: name,
            title: 'Bookmarks',
            permanent: false,
            spaceName: name,
            topBar: true,
            topLevelId: brwsr.BOOKMARKS_ROOT_FOLDER_ID,
            currentFolderId: folder || brwsr.BOOKMARKS_ROOT_FOLDER_ID,
            focusOnId: '',
            focusOnIndex: undefined,
            search: {
                text: !searchResult ? '' : searchResult.text,
                items: !searchResult ? [] : searchResult.items
            },
            view: 'grid'
        };
        if (typeof toIndex === 'undefined') {
            state.browsers.push(bookmarkBrowser);
        } else {
            state.browsers.splice(toIndex, 0, bookmarkBrowser);
        }
        this.commit(types.SET_ACTIVE_BOOKMARKS_BROWSER, name);
        sendBBchangeMessage();
    },
    [types.REMOVE_BB] (state, browserName) {
        let browserIndex = state.browsers.findIndex( b => b.name === browserName );
        if(browserIndex !== -1){
            let isActive = state.browsers[browserIndex].name === state.activeBrowser;
            state.browsers.splice(browserIndex, 1);
            if(isActive){
                this.commit(types.SET_ACTIVE_BOOKMARKS_BROWSER, state.browsers[browserIndex >= 1 ? browserIndex - 1 : 0].name);
            }
        }
        sendBBchangeMessage();
    },
    [types.RESTORE_BOOKMARKS_BROWSERS] (state) {
        let key = types.STORAGE_KEY_BROWSERS;
        if(window.localStorage.getItem(key) !== null){
            let browsers = JSON.parse(window.localStorage.getItem(key));
            Vue.set(state, 'browsers', browsers);
            let activeBrowserIndex = state.browsers.findIndex(b => b.name === state.activeBrowser)
            if(activeBrowserIndex === -1 && state.browsers.length){
                state.previousBrowserName = '';
                state.activeBrowser = state.browsers[0].name;
            }
            // for(let i=0; i < browsers.length; i++){
            //     if(state.browsers[i]){
            //         state.browsers[i].title = browsers[i].title;
            //         state.browsers[i].currentFolderId = browsers[i].currentFolderId;
            //         state.browsers[i].view = browsers[i].view;
            //     } else {
            //         state.browsers.push(browsers[i]);
            //     }
            // } // TODO
        }
    },
    [types.BB_FOLDER_BAR_MOVE_ITEM] (state, {name, toIndex}) {
        let fromIndex = state.browsers.findIndex(b => b.name === name);
        if(fromIndex > 0 && toIndex >= 2 && toIndex < state.browsers.length && toIndex !== fromIndex){
            state.browsers.splice(toIndex, 0, state.browsers.splice(fromIndex, 1)[0]);
        }
    },

    [types.RESTORE_COLUMNS_STATE] (state) {
        if(window.localStorage.getItem(types.STORAGE_KEY_COLUMNS) !== null){
            state.columns = JSON.parse(window.localStorage.getItem(types.STORAGE_KEY_COLUMNS));
            if(state.columns.left.width < 20 || state.columns.left.width > 1000){
                state.columns.left.width = 255;
            }
            if(state.columns.right.width < 20 || state.columns.right.width > 1000){
                state.columns.right.width = 255;
            }
        } else {
            // migration  TODO del
            // left
            if(window.localStorage.getItem(types.STORAGE_KEY_LEFT_COLUMN_DISPLAY) !== null){
                state.columns.left.display = JSON.parse(window.localStorage.getItem(types.STORAGE_KEY_LEFT_COLUMN_DISPLAY));
            }
            if(window.localStorage.getItem(types.STORAGE_KEY_LEFT_COLUMN_WIDTH) !== null){
                let width = Number(window.localStorage.getItem(types.STORAGE_KEY_LEFT_COLUMN_WIDTH));
                state.columns.left.width = width > 20 && width < 1000 ? width : 255; // TODO
            }
            // right
            if(window.localStorage.getItem(types.STORAGE_KEY_RIGHT_COLUMN_DISPLAY) !== null){
                state.columns.right.display = JSON.parse(window.localStorage.getItem(types.STORAGE_KEY_RIGHT_COLUMN_DISPLAY));
            }
            if(window.localStorage.getItem(types.STORAGE_KEY_RIGHT_COLUMN_WIDTH) !== null){
                let width = Number(window.localStorage.getItem(types.STORAGE_KEY_RIGHT_COLUMN_WIDTH));
                state.columns.right.width = width > 20 && width < 1000 ? width : 255; // TODO
            }
            if(window.localStorage.getItem(types.STORAGE_KEY_RIGHT_COLUMN_MENU) !== null){
                state.columns.right.activeMenu = window.localStorage.getItem(types.STORAGE_KEY_RIGHT_COLUMN_MENU);
            }
            window.localStorage.setItem(types.STORAGE_KEY_COLUMNS, JSON.stringify(state.columns));
        }
    },
    [types.CHANGE_COLUMN_WIDTH] (state, {columnId, width}) {
        if(state.columns[columnId]){
            state.columns[columnId].width = width > 20 && width < 1000 && Number.isInteger(width) ? width : 255;
        }
    },
    [types.COLUMN_TOGGLE] (state, {columnId}) {
        if(state.columns[columnId]){
            state.columns[columnId].display = !state.columns[columnId].display;
        }
    },
    [types.COLUMN_CHANGE_ACTIVE_MENU] (state, {columnId, menu}) {
        if(state.columns[columnId]){
            state.columns[columnId].activeMenu = menu;
        }
    },
    [types.TOPBAR_TOGGLE] (state) {
        state.showTopBar = !state.showTopBar;
    },
    [types.RESTORE_TOPBAR_STATE] (state) {
        if(window.localStorage.getItem(types.STORAGE_KEY_TOPBAR_DISPLAY) !== null){
            state.showTopBar = JSON.parse(window.localStorage.getItem(types.STORAGE_KEY_TOPBAR_DISPLAY));
        }
    },

    [types.TREE_CHANGE_FOLDER_STATE] (state, folderId) {
        if(state.openedFolders.indexOf(folderId) === -1){
            state.openedFolders.push(folderId);
        } else {
            state.openedFolders.splice(state.openedFolders.indexOf(folderId), 1);
        }
    },
    [types.RESTORE_TREE_FOLDERS_STATE] (state) {
        if(window.localStorage.getItem(types.STORAGE_KEY_TREE_OPENED_FOLDERS) !== null){
            state.openedFolders = JSON.parse(window.localStorage.getItem(types.STORAGE_KEY_TREE_OPENED_FOLDERS));
        }
    },

    [types.ADD_TO_CLIPBOARD] (state, bookmarkIds) {
        state.clipboard = [];
        state.clipboard = bookmarkIds;
    },
    [types.REMOVE_FROM_CLIPBOARD] (state, bookmarkId) {
        if(state.clipboard.indexOf(bookmarkId) !== -1){
            state.clipboard.splice(state.clipboard.indexOf(bookmarkId), 1);
        }
    },

    [types.ADD_TO_RECENT_HIDDEN_ITEMS] (state, uniqId) {
        state.recentHiddenItems.push(uniqId);
    },

    [types.LOAD_SETTINGS] (state) {
        state.settings.load = true;
    },
    [types.SHOW_SETTINGS] (state) {
        state.settings.display = true;
    },
    [types.HIDE_SETTINGS] (state) {
        state.settings.display = false;
    },

    [types.SET_BACKGROUND] (state, background) {
        if(background){
            switch (background.type) {
                case 'image':
                case 'pattern':
                    state.decor.background.id = background.tmb || '';
                    state.decor.background.url = background.url;
                    state.decor.background.type = background.type;
                    state.decor.background.gamma = background.colorState ? background.colorState.gamma : -1;
                    state.decor.background.color = [];
                    if(background.author){
                        Vue.set(state.decor.background, 'author', background.author);
                    } else {
                        state.decor.background.author = null;
                    }
                    break;
                case 'color':
                    state.decor.background.color = background.color;
                    // state.decor.background.type = background.type;
                    // state.decor.background.id = '';
                    // state.decor.background.url = '';
                    // state.decor.background.gamma = -1;
                    // state.decor.background.author = null;
                    break;
            }
        } else {
            state.decor.background.id = '';
            state.decor.background.url = '';
            state.decor.background.type = '';
            state.decor.background.author = null;
            state.decor.background.gamma = -1;
        }
    },
    [types.SET_DECOR_PROP] (state, {prop, value}) {
        // console.log('Store.state.setDecor', prop, value);
        prop.split('.').reduce((accumulator, currentValue, index, array) => {
            if(index === array.length - 1 && accumulator && accumulator.hasOwnProperty(currentValue)){
                // console.log('Store.state.setDecor.===', accumulator, currentValue, value, state);
                accumulator[currentValue] = value;
            }
            // if(prop === 'background.color' && Array.isArray(value) && value.length){
            //     state.decor.background.id = '';
            //     state.decor.background.url = '';
            //     state.decor.background.type = 'color';
            //     state.decor.background.author = null;
            //     state.decor.background.gamma = -1;
            // } // TODO
            return accumulator[currentValue]; // TODO if no property // Cannot read property 'backgroundColor' of undefined
        }, state.decor);
        // https://stackoverflow.com/questions/8051975/access-object-child-properties-using-a-dot-notation-string
    },

    [types.SET_UNSPLASH_PHOTOS] (state, {photos, modify}) {
        state.decor.background.sets.unsplash.modify = modify;
        state.decor.background.sets.unsplash.photos = photos;
    },

    [types.SET_ACTIVE_SPACE] (state, spaceName) {
        state.activeSpace = spaceName;
        // let index = state.browsers.findIndex(b => b.name === spaceName);
        // if(index >= 0){
        //     state.activeSpace = spaceName;
        // }
        // Not only browser can hook active space for keyboard input TODO (search)
    },
    [types.SET_ACTIVE_SPACE_BY_BROWSER_NAME] (state, browserName) {
        let browser = state.browsers.find( b => b.name === browserName );
        if(browser){
            state.activeSpace = browser.spaceName;
        }
    },

    [types.LOAD_WHAT_IS_NEW] (state, version) {
        if(window.localStorage.getItem(types.STORAGE_KEY_WHAT_IS_NEW_VERSION) !== version){
            state.whatIsNewAvailable = true;
        }
    },
    [types.LOAD_NEW_SETTINGS_AVAILABLE] (state, version) {
        if(Number(window.localStorage.getItem(types.STORAGE_KEY_SETTINGS_VERSION)) !== state.settings.version){
            state.settings.newSettingsAvailable = true;
        }
    },
    [types.SET_OVERRIDE_ITEM] (state, {override, item}) {
        if(override.iconUrl){
            Vue.set(state.iconOverrides, item.url, override.iconUrl);
        } else {
            Vue.delete(state.iconOverrides, item.url);
        }
        if(override.iconHostname){
            if(override.iconUrl){
                Vue.set(state.iconOverrides, override.iconHostname, override.iconUrl);
            } else {
                Vue.delete(state.iconOverrides, override.iconHostname);
            }
        }
        if(override.tilePicUrl){
            Vue.set(state.tilePicOverrides, item.url, override.tilePicUrl);
        } else {
            Vue.delete(state.tilePicOverrides, item.url);
        }
        if(override.tileTitle){
            Vue.set(state.tileTitleOverrides, item.url, override.tileTitle);
        } else {
            Vue.delete(state.tileTitleOverrides, item.url);
        }
    },
    [types.DELETE_OVERRIDDEN_ITEM] (state, {type, bookmarkUrl}) {
        if(type === types.OVERRIDDEN_ITEM_TYPE_ICON){
            Vue.delete(state.iconOverrides, bookmarkUrl);
        }
        if(type === types.OVERRIDDEN_ITEM_TYPE_TILE){
            Vue.delete(state.tilePicOverrides, bookmarkUrl);
        }
    },
    [types.OPEN_OVERRIDES_MANAGER] (state) {
        state.showOverridesManager = true;
    },
    [types.CLOSE_OVERRIDES_MANAGER] (state) {
        state.showOverridesManager = false;
    },
    [types.MERGE_OVERRIDES] (state, {type, overrides}) {
        if(type === types.OVERRIDDEN_ITEM_TYPE_ICON){
            for (let prop in overrides) {
                Vue.set(state.iconOverrides, prop, overrides[prop]);
            }
        }
        if(type === types.OVERRIDDEN_ITEM_TYPE_TILE){
            for (let prop in overrides) {
                Vue.set(state.tilePicOverrides, prop, overrides[prop]);
            }
        }
        if(type === types.OVERRIDDEN_ITEM_TYPE_TILE_TITLE){
            for (let prop in overrides) {
                Vue.set(state.tileTitleOverrides, prop, overrides[prop]);
            }
        }
    },

    [types.UPDATE_COLOR_IN_CACHE] (state, {domain, color}) {
        state.colorsCache.items[domain] = color;
    },
    [types.RESET_COLORS_CACHE] (state) {
        state.colorsCache.items = {};
        state.colorsCache.lastReset = Math.floor(Date.now() / 1000); // https://stackoverflow.com/questions/221294/how-do-you-get-a-timestamp-in-javascript
    }, // TODO not used
};

let bookmarksChangeTimer;

function sendBBchangeMessage() {
    window.clearTimeout(bookmarksChangeTimer);
    bookmarksChangeTimer = window.setTimeout(()=>{
        brwsr.sendRuntimeMessage({
            text: types.MSG_BROWSERS_CHANGED
        });
    }, 1000);
}