import * as types from '../../store/mutation-types'
import BrowserItem from './browser_item.vue'
import BrowserNavi from './browser_navi.vue'
import ViewSwitcher from './view_switcher.vue'
import brwsr from '../../services/browser'

export default {
    components: {
        BrowserItem,
        BrowserNavi,
        ViewSwitcher
    },
    props: [
        'model',
        'tileDecor',
        'decor'
    ],
    data () {
        return {
            items: [],
            currentFolder: {},
            selectedItems: [], // items indexes
            selectionInitiator: {
                index: -1,
                key: '' // ctrl, shift
            },
            focusOnFirstItemAfterChangeFolder: false,
            backBtnIndex: -1,
            width: '',
            itemsInRow: 0,
            viewTypes: {
                grid: 'grid',
                list: 'list',
                tile: 'tile',
                compact: 'compact'
            },
            ready: true,
            activated: false,
            goBackTimeout: null,
            dragStartedFromMe: false
        }
    },
    computed: {
        classes () {
            return {
                'width-xs': this.width < 300,
                'width-s': this.width >= 300 && this.width < 600,
                'width-m': this.width >= 600 && this.width < 700,
                'width-l': this.width >= 700 && this.width < 1000,
                'width-xl': this.width >= 1000 && this.width < 1200,
                'width-xxl': this.width >= 1200 && this.width < 1400,
                'width-xxxl': this.width >= 1400,
                'space-active':this.isSpaceActive
            }
        },
        browserClasses () {
            return {
                'browser-view-grid': 	this.viewType === this.viewTypes.grid,
                'browser-view-list': 	this.viewType === this.viewTypes.list,
                'browser-view-tile': 	this.viewType === this.viewTypes.tile,
                'browser-view-compact': this.viewType === this.viewTypes.compact
            }
        },
        isActive () {
            return this.model.name === this.$store.state.activeBrowser;
        },
        isFolderFound () {
            return this.currentFolder.id;
        },
        isSpaceActive () {
            return this.model.spaceName === this.$store.state.activeSpace;
        },
        isBackBtn () {
            if(this.isSearch){
                return false;
            } else if (this.model.currentFolderId === this.model.topLevelId) {
                return false;
            }
            return true;
        },
        isSearch () {
            return this.model.search.text;
        },
        clipboard () {
            return this.$store.state.clipboard;
        },
        dragItem () {
            return this.$store.state.dragItem;
        },
        prevBrowser () {
            return this.$store.state.previousBrowserName;
        },
        is4directions () {
            return this.viewType === this.viewTypes.grid ||
                this.viewType === this.viewTypes.tile ||
                this.viewType === this.viewTypes.list;
        },
        backBtnFocusedByKeyboard () {
            return this.model.focusOnIndex === this.backBtnIndex;
        },
        topBarStyles () {
            return {
                'visibility': (this.$store.state.config.useVisibleOptionForHideColumnTopBar && !this.model.topBar) ? 'hidden' : '',
            }
        },
        isTopbar () {
            return this.model.topBar || (this.$store.state.config.useVisibleOptionForHideColumnTopBar && !this.model.topBar);
        },
        viewType () {
            const folderConfig = this.$store.state.foldersConfig[this.model.currentFolderId];
            if(folderConfig && folderConfig.view && folderConfig.view !== this.model.view){
                return folderConfig.view;
            }
            return this.model.view;
        }
    },
    mounted () {
        if(this.isActive){
            this.activated = true;
            if(!this.isSearch){
                this.getItems(this.model.currentFolderId);
            } else {
                this.items = this.model.search.items;
                this.$store.commit(types.BB_SET_TITLE, {
                    browserName: this.model.name,
                    title: 'Search "'+ this.model.search.text +'"'
                });
            }
            this.hookFocus();
        }
        this.eventsHandler();

        window.addEventListener('keydown', this.onKeyDown);
        window.addEventListener('resize', this.calcWidth);

        this.$nextTick( () => {
            this.calcWidth();
        });
    },
    watch: {
        'model.currentFolderId': function (newFolderId, oldVal) {
            this.deselectItems();
            this.$el.querySelector('.column-content').scrollTop = 0;
            if(this.isSearch){
                this.$store.commit(types.BB_RESET_SEARCH, this.model.name);
            }
            this.getItems(newFolderId);
            if(this.focusOnFirstItemAfterChangeFolder){
                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                    browserName: this.model.name,
                    index: this.isBackBtn ? this.backBtnIndex : 0
                });
                this.focusOnFirstItemAfterChangeFolder = false;
            }
        },
        'backBtnFocusedByKeyboard': function (newVal, oldVal) {
            if(newVal){
                let browserElm = this.$el.querySelector('.browser');
                if(browserElm){
                    browserElm.children[0].scrollIntoViewIfNeeded()
                }
                // this.$el.getElementsByClassName('browser')[0].children[0].scrollIntoView();
            }
        },
        'isActive': function (newVal, oldVal) {
            if(newVal){
                this.calcWidth();
                this.$store.commit(types.SET_ACTIVE_SPACE, this.model.spaceName);
                if(!this.activated){
                    this.activated = true;
                    if(!this.isSearch){
                        this.getItems(this.model.currentFolderId);
                    } else {
                        this.items = this.model.search.items;
                    }
                }
            }
        },
        'dragItem': {
            handler: function () {
                if(!this.dragItem.data && this.dragStartedFromMe){
                    this.deselectItems();
                }
            },
            deep: true
        },
        '$store.state.columns': {
            handler: function () {
                this.calcWidth();
            },
            deep: true
        },
    },
    methods: {
        eventsHandler () {
            this.$root.$on('bookmarkCreated', (bookmark) => {
                if(!this.isSearch && this.currentFolder && this.currentFolder.id === bookmark.parentId){
                    this.getItems(this.currentFolder.id);
                }
            });
            this.$root.$on('bookmarkMoved', (id, info, bookmark) => {
                if(!this.isSearch && this.currentFolder.id === info.parentId || this.currentFolder.id === info.oldParentId){
                    this.getItems(this.currentFolder.id);
                }
            });
            this.$root.$on('bookmarkRemoved', (id, info) => {
                if(!this.isSearch){
                    if(this.currentFolder.id === info.parentId){
                        this.getItems(this.currentFolder.id);
                    } else if(this.currentFolder.id === id){
                        this.model.permanent ? this.goBack(false) : this.close();
                    } else if(brwsr.isBookmarkFolder(info.node)){
                        this.isFolderStillAlive();
                    }
                }
            });
            this.$root.$on('bookmarkChanged', (bookmark) => {
                if(!this.isSearch){
                    if(this.currentFolder.id === bookmark.parentId){
                        this.getItems(this.currentFolder.id);
                    } else if(this.currentFolder.id === bookmark.id){
                        this.$store.commit(types.BB_SET_TITLE, {
                            browserName: this.model.name,
                            title: bookmark.title
                        });
                    }
                }
            });
            this.$root.$on('close-bookmark-browser', (browserName) => {
                if(this.model.name === browserName){
                    this.close();
                }
            });
        },
        calcWidth () {
            this.$nextTick( () => {
                this.width = this.$el.clientWidth;
            });
            // this.width = this.$el.getBoundingClientRect().width;
        },
        setItemWidth (payload) {
            this.itemsInRow = Math.floor(this.width / payload.width);
        },
        setCurrentFolder (bookmarkFolder = {}) {
            this.$set(this, 'currentFolder', bookmarkFolder);
            this.setTitle ();
        },
        setTitle () {
            if(this.isFolderFound){
                if(!this.model.quickStart){
                    this.$store.commit(types.BB_SET_TITLE, {
                        browserName: this.model.name,
                        title: this.currentFolder.id !== brwsr.BOOKMARKS_ROOT_FOLDER_ID ? this.currentFolder.title : 'Bookmarks'
                    });
                } else {
                    this.$store.commit(types.BB_SET_TITLE, {
                        browserName: this.model.name,
                        title: this.currentFolder.id !== this.model.topLevelId ? this.currentFolder.title : 'Quick start'
                    });
                } // TODO
            } else {
                this.$store.commit(types.BB_SET_TITLE, {
                    browserName: this.model.name,
                    title: '*Not found*'
                });
            }
        },
        getItems (folderId) {
            brwsr.getBookmark(folderId).then((bms) => {
                if(brwsr.isBookmarkFolder(bms[0])){
                    this.setCurrentFolder(bms[0]);
                    brwsr.getBookmarkChildren(folderId).then((bookmarks) => {
                        this.items = bookmarks;
                    });
                } else {
                    this.getItems(this.model.topLevelId);
                }
            }).catch(() => {
                this.setCurrentFolder();
            });
        },
        selectItem ({item, mouseEvent}) { // TODO
            if(this.selectedItems.length === 0){
                window.document.addEventListener('click', this.deselectItems);
            }
            if(this.selectionInitiator.index === -1){
                this.selectionInitiator.index = item.index;
                this.selectionInitiator.key = mouseEvent.ctrlKey ? 'ctrl' : 'shift';
            }
            if(mouseEvent.ctrlKey){
                let index = this.selectedItems.indexOf(item.index);
                if(index === -1){
                    this.selectedItems.push(item.index);
                } else {
                    this.selectedItems.splice(index, 1);
                }
            }
            if(mouseEvent.shiftKey){
                if(this.selectedItems.length){
                    if(this.selectionInitiator.key === 'ctrl'){
                        const minIndex = Math.min(...this.selectedItems);
                        const maxIndex = Math.max(...this.selectedItems);
                        if(item.index < minIndex){
                            this.selectedItems = [];
                            for(let i = minIndex; i >= item.index; i--){
                                this.selectedItems.push(i);
                            }
                        } else if(item.index > maxIndex){
                            this.selectedItems = [];
                            for(let i = maxIndex; i <= item.index; i++){
                                this.selectedItems.push(i);
                            }
                        }
                    } else if (this.selectionInitiator.key === 'shift') {
                        const startIndex = this.selectionInitiator.index;
                        if(item.index <= startIndex){
                            this.selectedItems = [];
                            for(let i = startIndex; i >= item.index; i--){
                                this.selectedItems.push(i);
                            }
                        } else if(item.index >= startIndex){
                            this.selectedItems = [];
                            for(let i = startIndex; i <= item.index; i++){
                                this.selectedItems.push(i);
                            }
                        }
                    }
                } else {
                    this.selectedItems.push(item.index);
                }
            }
            this.selectedItems.sort((a, b) => a - b);
        },
        deselectItems () {
            if(this.selectionInitiator.index !== -1){
                this.selectionInitiator.index = -1;
                this.selectionInitiator.key = '';
                this.selectedItems = [];
                window.document.removeEventListener('click', this.deselectItems);
            }
        },
        cutItems (bookmark) {
            let ids = [];
            if(this.selectedItems.length){
                for(let i = 0; i < this.selectedItems.length; i++){
                    ids.push(this.items[this.selectedItems[i]].id);
                }
            } else if(bookmark){
                ids.push(bookmark.id);
            }
            this.$store.commit(types.ADD_TO_CLIPBOARD, ids);
        },
        deleteItems (bookmark) {
            const items = this.getSelectedItems(bookmark);
            for(let i = 0; i < items.length; i++){
                let bm = items[i];
                if(!brwsr.isBookmarkFolder(bm)){
                    brwsr.removeBookmark(bm.id)
                } else {
                    brwsr.removeBookmarksTree(bm.id);
                }
                window.setTimeout(this.deselectItems, 200);
            }
        },
        openItems ({bookmark, props = {}, newWindow}) {
            const items = this.getSelectedItems(bookmark);
            if(newWindow) {
                let urls = [];
                items.forEach((bm) => {
                    if(!brwsr.isBookmarkFolder(bm)){
                        urls.push(bm.url);
                    }
                });
                brwsr.createWindow({
                    url: urls,
                    ...props
                });
            } else {
                items.forEach((bm) => {
                    if(!brwsr.isBookmarkFolder(bm)){
                        brwsr.createTab({
                            url: bm.url,
                            ...props
                        });
                    }
                });
            }
        },
        addToQuickStart (bookmark) {
            if(this.$store.state.quickStartFolder.hasOwnProperty('id')){
                let items = this.getSelectedItems(bookmark);
                for(let i = 0; i < items.length; i++){
                    let bm = items[i];
                    if(bm.url){
                        brwsr.createBookmark({
                            title: bm.title,
                            url: bm.url,
                            parentId: this.$store.state.quickStartFolder.id
                        });
                    }
                }
            }
        },
        getSelectedItems (bookmark) {
            let items = [];
            if(this.selectedItems.length){
                for(let i = 0; i < this.selectedItems.length; i++){
                    items.push(this.items[this.selectedItems[i]]);
                }
            } else if(bookmark){
                items.push(bookmark);
            }
            return items;
        },
        goBack (eventFromKeyboard, e) {
            if(this.currentFolder.parentId){
                if(this.model.focusOnId){
                    this.$store.commit(types.BB_RESET_SEARCH, this.model.name);
                }
                brwsr.getBookmark(this.currentFolder.parentId).then(() => {
                    this.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
                        browserName: this.model.name,
                        folderId: this.currentFolder.parentId
                    });
                }).catch(() => {
                    this.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
                        browserName: this.model.name,
                        folderId: this.model.topLevelId
                    });
                });
            }
            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                browserName: this.model.name,
                index: eventFromKeyboard ? this.backBtnIndex : undefined
            });
        },
        isFolderStillAlive () {
            if(this.isFolderFound){
                brwsr.getBookmark(this.currentFolder.id).then().catch(() => {
                    this.setCurrentFolder();
                });
            }
        },
        setDragItems ({item, event}) {
            this.$store.commit(types.SET_DRAG_ITEM, {
                type: !brwsr.isBookmarkFolder(item) ? types.DRAG_ITEM_TYPE_BOOKMARK : types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER,
                startEvent: event,
                data: this.selectedItems.length ? this.getSelectedItems(item) : item
            });
            this.dragStartedFromMe = true;
        },
        dragOver (e) {

        },
        drop (e) {
            if(!this.isSearch){
                let items = Array.isArray(this.dragItem.data) ? this.dragItem.data : [this.dragItem.data];
                switch (this.dragItem.type) {
                    case types.DRAG_ITEM_TYPE_BOOKMARK:
                    case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                        for(let i = 0; i < items.length; i++){
                            if(this.currentFolder.id !== items[i].parentId){
                                brwsr.moveBookmark(items[i].id, {
                                    parentId: this.currentFolder.id
                                });
                            }
                        }
                        break;
                    case types.DRAG_ITEM_TYPE_TAB:
                    case types.DRAG_ITEM_TYPE_RECENT:
                    case types.DRAG_ITEM_TYPE_TOP_SITE:
                        for(let i = 0; i < items.length; i++){
                            brwsr.createBookmark({
                                url: items[i].url,
                                title: items[i].title,
                                parentId: this.currentFolder.id
                            });
                        }
                        break;
                    case types.DRAG_ITEM_TYPE_BROWSER_WINDOW:
                        const currentDate = new Date();
                        const tabs = this.dragItem.data.tabs;
                        brwsr.createBookmark({
                            title: 'Window '+ currentDate.toLocaleString(),
                            parentId: this.currentFolder.id
                        }).then((folder) => {
                            tabs.forEach((tab, index) => {
                                window.setTimeout(() => {
                                    brwsr.createBookmark({
                                        title: tab.title,
                                        url: tab.url,
                                        parentId: folder.id
                                    });
                                }, index * 50);
                            });
                        });
                        break;
                }
                this.$store.commit(types.SET_DRAG_ITEM, {});
            }
        },
        dragEnterBackButton (e) {
            switch (this.dragItem.type) {
                case types.DRAG_ITEM_TYPE_BOOKMARK:
                case types.DRAG_ITEM_TYPE_TAB:
                case types.DRAG_ITEM_TYPE_RECENT:
                case types.DRAG_ITEM_TYPE_TOP_SITE:
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    window.clearTimeout(this.goBackTimeout);
                    this.goBackTimeout = window.setTimeout(() => {
                        this.goBack();
                    }, 600);
                    break;
            }
        },
        dragLeaveBackButton (e) {
            window.clearTimeout(this.goBackTimeout);
        },
        onKeyDown (e) {
            if(this.isSpaceActive){
                // console.log(e.code, e.key);
                switch (e.code) {
                    case 'ArrowUp':
                        if(typeof this.model.focusOnIndex === 'undefined'){
                            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                browserName: this.model.name,
                                index: !this.isBackBtn ? 0 : this.backBtnIndex
                            });
                            break;
                        }
                        if(this.is4directions){
                            if(this.model.focusOnIndex - this.itemsInRow >= this.backBtnIndex){
                                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                    browserName: this.model.name,
                                    index: this.model.focusOnIndex - this.itemsInRow
                                });
                            } else {
                                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                    browserName: this.model.name,
                                    index: !this.isBackBtn && this.model.focusOnIndex - this.itemsInRow <= this.backBtnIndex ? 0 : this.backBtnIndex
                                });
                            }
                        } else {
                            if(this.model.focusOnIndex > this.backBtnIndex){
                                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                    browserName: this.model.name,
                                    index: !this.isBackBtn && this.model.focusOnIndex - this.itemsInRow <= this.backBtnIndex ? 0 : this.model.focusOnIndex - 1
                                });
                            }
                        }
                        e.returnValue = false;
                        break;
                    case 'ArrowRight':
                        if(typeof this.model.focusOnIndex === 'undefined'){
                            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                browserName: this.model.name,
                                index: !this.isBackBtn ? 0 : this.backBtnIndex
                            });
                            break;
                        }
                        if(this.model.focusOnIndex < this.items.length - 1 && this.is4directions){
                            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {browserName: this.model.name, index: this.model.focusOnIndex+1});
                        }
                        e.returnValue = false;
                        break;
                    case 'ArrowDown':
                        if(typeof this.model.focusOnIndex === 'undefined'){
                            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                browserName: this.model.name,
                                index: !this.isBackBtn ? 0 : this.backBtnIndex
                            });
                            break;
                        }
                        if(this.is4directions){
                            if(this.model.focusOnIndex + this.itemsInRow <= this.items.length - 1){
                                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                    browserName: this.model.name,
                                    index: this.model.focusOnIndex + this.itemsInRow
                                });
                            } else {
                                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                    browserName: this.model.name,
                                    index: this.items.length - 1
                                });
                            }
                        } else {
                            if(this.model.focusOnIndex < this.items.length - 1){
                                this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                    browserName: this.model.name,
                                    index: this.model.focusOnIndex + 1
                                });
                            }
                        }
                        e.returnValue = false;
                        break;
                    case 'ArrowLeft':
                        if(typeof this.model.focusOnIndex === 'undefined'){
                            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                browserName: this.model.name,
                                index: !this.isBackBtn ? 0 : this.backBtnIndex
                            });
                            break;
                        }
                        if(this.model.focusOnIndex > this.backBtnIndex && this.is4directions){
                            this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                                browserName: this.model.name,
                                index: !this.isBackBtn && (this.model.focusOnIndex - 1) === this.backBtnIndex ? this.model.focusOnIndex : this.model.focusOnIndex - 1
                            });
                        }
                        e.returnValue = false;
                        break;
                    case 'Enter':
                        this.onKeyEnterDown(e);
                        break;
                    case 'NumpadEnter':
                        this.onKeyEnterDown(e);
                        break;
                    // case 'F2':
                    //     this.$set(this.items[this.model.focusOnIndex], 'rename', true);
                    //     e.returnValue = false;
                    //     break;
                    case 'Backspace':
                        if(this.isBackBtn){
                            this.goBack(true);
                            this.focusOnFirstItemAfterChangeFolder = true;
                        }
                        e.returnValue = false;
                        break;
                    case 'KeyX':
                        if(e.ctrlKey){
                            this.cutItems(this.items[this.model.focusOnIndex]);
                            // this.$store.commit(types.ADD_TO_CLIPBOARD, this.items[this.model.focusOnIndex].id);
                        }
                        e.returnValue = false;
                        break;
                    case 'KeyV':
                        if(e.ctrlKey){
                            this.pasteItems();
                        }
                        e.returnValue = false;
                        break;
                    case 'Home':
                        this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {
                            browserName: this.model.name,
                            index: this.isBackBtn ? this.backBtnIndex : 0
                        }); // TODO
                        e.returnValue = false;
                        break;
                    case 'End':
                        this.$store.commit(types.BB_SET_FOCUS_ON_INDEX, {browserName: this.model.name, index: this.items.length - 1});
                        e.returnValue = false;
                        break;
                }
            }
            // return false;
        },
        onKeyEnterDown (e) {
            if(this.model.focusOnIndex > this.backBtnIndex){
                let item = this.items[this.model.focusOnIndex];
                if(!e.ctrlKey){
                    if(item.url){
                        window.location.replace(item.url);
                    } else {
                        this.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
                            browserName: this.model.name,
                            folderId: item.id
                        });
                        this.focusOnFirstItemAfterChangeFolder = true;
                    }
                } else {
                    this.$root.$emit('show-context-menu-for-id', item.id);
                }
            } else if(this.model.focusOnIndex === this.backBtnIndex) {
                this.goBack(true);
                this.focusOnFirstItemAfterChangeFolder = true;
            }
            e.returnValue = false;
        },
        hookFocus (e) {
            if(!this.isSpaceActive){
                this.$store.commit(types.SET_ACTIVE_SPACE, this.model.spaceName);
            }
        },
        pasteItems () {
            let clipboard = this.clipboard;
            if(clipboard.length > 0){
                for(let i=0; i < clipboard.length; i++){
                    if(clipboard[i] !== this.currentFolder.id){
                        brwsr.moveBookmark(clipboard[i], {
                            parentId: this.currentFolder.id
                        }).then((bookmark) => {
                            this.$store.commit(types.REMOVE_FROM_CLIPBOARD, bookmark.id); // TODO ?
                        });
                    }
                }
            }
        },
        close () {
            if(!this.model.permanent){
                window.removeEventListener('resize', this.calcWidth);
                window.removeEventListener('keydown', this.onKeyDown);
                this.$store.commit(types.REMOVE_BB, this.model.name);
            }
        },
        showContextMenu (e) {
            if(this.currentFolder.id !== brwsr.BOOKMARKS_ROOT_FOLDER_ID && !this.isSearch){
                let items = [];
                items.push({
                    title: 'Paste',
                    vm: this,
                    inactive: this.clipboard.length === 0,
                    clickFn: function (item, menu) {
                        item.vm.pasteItems();
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Add folder',
                    vm: this,
                    clickFn: function (item, menu) {
                        brwsr.createBookmark({
                            parentId: item.vm.currentFolder.id,
                            title: 'New folder'
                        }).then((bookmark) => {
                            window.setTimeout(() => {
                                let bmModel = item.vm.items.find( i => i.id === bookmark.id);
                                if(bmModel){
                                    item.vm.$set(bmModel, 'browserForceRename', true);
                                }
                            }, 300);
                        });
                        menu.hide();
                    }
                });
                items.push({
                    title: 'Add bookmark',
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.$root.$emit('dialog-create-bookmark', item.vm.currentFolder.id);
                        menu.hide();
                    }
                });
                this.$root.$emit('show-context-menu', {
                    left: e.pageX,
                    top: e.pageY,
                    items: items
                })
            }
        }
    }
}