import RecentItem from './recent_item.vue'
export default {
	components: {
		RecentItem
	},
	props: [
		'model'
	],
	data () {
		return {
			opened: false,
			sessions: []
		}
	},
	computed: {
		show () {
			return this.sessions.length > 0;
		}
	},
	mounted () {
		if(this.model.sessions.length === 1){
			for(let i=0; i < this.model.sessions[0].window.tabs.length; i++){
				this.model.sessions[0].window.tabs[i].windowSessionId = this.model.sessions[0].window.sessionId;
				this.sessions.push({
					tab: this.model.sessions[0].window.tabs[i]
				});
			}
			this.opened = true;
		} else {
			this.sessions = this.model.sessions;
		}
	},
	methods: {
		open () {
			this.opened = !this.opened;
		}
	}
}
