import TabsItem from './tabs_item.vue'
import * as types from '../../store/mutation-types'
import brwsr from '../../services/browser'

export default {
    components: {
        TabsItem
    },
    data: function () {
        return {
            items: [],
            openedWindows: [],
            selection: {
                items: [], // indexes
                startIndex: -1,
                windowId: '',
                startKey: '' //ctrl, shift
            },
            selectedItems: [],
            toggleTimeout: {}
        }
    },
    mounted () {
        brwsr.getCurrentWindow().then((window) => {
            this.toggle(window.id);
            this.getItems();
        });
        this.setEventsHandlers();
    },
    computed: {
        dragItem () {
            return this.$store.state.dragItem;
        },
        isDragInProgress () {
            return this.dragItem.type === types.DRAG_ITEM_TYPE_TAB ||
                this.dragItem.type === types.DRAG_ITEM_TYPE_BOOKMARK ||
                this.dragItem.type === types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER;
        }
    },
    methods: {
        setEventsHandlers () {
            brwsr.addListenerToTabs('onCreated', this.getItems);
            brwsr.addListenerToTabs('onUpdated', this.getItems);
            brwsr.addListenerToTabs('onMoved', this.getItems);
            brwsr.addListenerToTabs('onActivated', this.getItems);
            brwsr.addListenerToTabs('onDetached', this.getItems);
            brwsr.addListenerToTabs('onAttached', this.getItems);
            brwsr.addListenerToTabs('onRemoved', this.getItems);
            brwsr.addListenerToWindows('onCreated', this.getItems);
            brwsr.addListenerToWindows('onRemoved', this.getItems);
            brwsr.addListenerToWindows('onFocusChanged', this.getItems);
            // TODO
        },
        toggle (windowId) {
            const index = this.openedWindows.indexOf(windowId);
            if(index !== -1){
                this.openedWindows.splice(index, 1);
            } else {
                this.openedWindows.push(windowId);
            }
        },
        tryToToggleWindow (windowId) {
            window.clearTimeout(this.toggleTimeout[windowId]);
            this.toggleTimeout[windowId] = window.setTimeout(() => {
                const index = this.openedWindows.indexOf(windowId);
                if(index === -1){
                    this.openedWindows.push(windowId);
                }
            }, 600);
        },
        getItems () {
            brwsr.getAllWindows({populate: true}).then((windows) => {
                this.items = windows;
            });
        },
        createTabs ({items, windowId, index}) {
            let urls = [];
            let promises = [];
            for(let i = 0; i < items.length; i++){
                if(items[i].url){
                    urls.push(items[i].url);
                } else {
                    let promise = brwsr.getBookmarkChildren(items[i].id).then((bookmarks) => {
                        if(bookmarks.length) {
                            for(let i=0; i < bookmarks.length; i++ ){
                                if(bookmarks[i].url){
                                    urls.push(bookmarks[i].url);
                                }
                            }
                        }
                    });
                    promises.push(promise);
                }
            }
            Promise.all(promises).then(() => {
                if(windowId) {
                    urls.forEach((url) => {
                        brwsr.createTab({
                            url: url,
                            active: false,
                            windowId: windowId,
                            index: index
                        });
                    });
                } else {
                    brwsr.createWindow({
                        url: urls,
                        state: 'maximized'
                    });
                }
            });

        },
        getWindowTitle (browserWindow) {
            return !this.isDragInProgress ? 'Window [' + browserWindow.tabs.length +']' :
                'Create New Window';
        },
        closeWindow (windowId) {
            brwsr.removeWindow(windowId);
        },
        isShowTabs (browserWindow) {
            return this.openedWindows.indexOf(browserWindow.id) !== -1;
        },
        windowDragStart (browserWindow) {
            this.$store.commit(types.SET_DRAG_ITEM, {
                type: types.DRAG_ITEM_TYPE_BROWSER_WINDOW,
                data: browserWindow
            });
        },
        windowDragEnter (browserWindow, e) {
            if(!this.isShowTabs(browserWindow)){
                this.tryToToggleWindow(browserWindow.id);
            }
        },
        windowDragLeave (browserWindow, e) {
            window.clearTimeout(this.toggleTimeout[browserWindow.id]);
        },
        dropInWindow (browserWindow, e) {
            let items = Array.isArray(this.dragItem.data) ? this.dragItem.data : [this.dragItem.data];
            switch (this.dragItem.type) {
                case types.DRAG_ITEM_TYPE_TAB:
                    brwsr.createWindow({
                        state: 'maximized'
                    }).then((window) => {
                        for(let i = 0; i < items.length; i++){
                            brwsr.moveTabs(items[i].id, {
                                windowId: window.id,
                                index: -1
                            });
                        }
                    });
                    this.deselectItems();
                    break;
                case types.DRAG_ITEM_TYPE_BOOKMARK:
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    this.createTabs({items: items});
                    break;
            }
            this.$store.commit(types.RESET_DRAG_ITEM);
            window.clearTimeout(this.toggleTimeout[browserWindow.id]);
        },
        selectItem ({item, mouseEvent}) { // TODO
            if(this.selection.startIndex === -1){
                this.selection.startIndex = item.index;
                this.selection.windowId = item.windowId;
                this.selection.startKey = mouseEvent.ctrlKey ? 'ctrl' : 'shift';
            }
            if(mouseEvent.ctrlKey && this.selection.windowId === item.windowId){
                let index = this.selection.items.findIndex(i => i.windowId === item.windowId && i.index === item.index);
                if(index === -1){
                    this.selection.items.push({
                        windowId: item.windowId,
                        index: item.index
                    });
                } else {
                    this.selection.items.splice(index, 1);
                }
            }
            if(mouseEvent.shiftKey && this.selection.windowId === item.windowId){
                if(this.selection.items.length){
                    if(this.selection.startKey === 'ctrl'){
                        const indexes = [];
                        this.selection.items.forEach((i) => {
                            indexes.push(i.index);
                        });
                        const minIndex = Math.min(...indexes);
                        const maxIndex = Math.max(...indexes);
                        if(item.index < minIndex){
                            this.selection.items = [];
                            for(let i = minIndex; i >= item.index; i--){
                                this.selection.items.push({
                                    windowId: item.windowId,
                                    index: i
                                });
                            }
                        } else if(item.index > maxIndex){
                            this.selection.items = [];
                            for(let i = maxIndex; i <= item.index; i++){
                                this.selection.items.push({
                                    windowId: item.windowId,
                                    index: i
                                });
                            }
                        }
                    } else if (this.selection.startKey === 'shift') {
                        const startIndex = this.selection.startIndex;
                        if(item.index <= startIndex){
                            this.selection.items = [];
                            for(let i = startIndex; i >= item.index; i--){
                                this.selection.items.push({
                                    windowId: item.windowId,
                                    index: i
                                });
                            }
                        } else if(item.index >= startIndex){
                            this.selection.items = [];
                            for(let i = startIndex; i <= item.index; i++){
                                this.selection.items.push({
                                    windowId: item.windowId,
                                    index: i
                                });
                            }
                        }
                    }
                } else {
                    this.selection.items.push({
                        windowId: item.windowId,
                        index: item.index
                    });
                }
            }
            this.selection.items.sort((a, b) => a.index - b.index);
            window.document.addEventListener('click', this.deselectItems);
        },
        deselectItems () {
            if(this.selection.startIndex !== -1){
                this.selection.startIndex = -1;
                this.selection.windowId = '';
                this.selection.startKey = '';
                this.selection.items = [];
                window.document.removeEventListener('click', this.deselectItems);
            }
        },
        checkSelection (tab) {
            let index = this.selection.items.findIndex(item => item.windowId === tab.windowId && item.index === tab.index);
            return index !== -1;
        },
        setDragItems ({item, event}) {
            if(item.windowId !== this.selection.windowId){
                this.deselectItems();
            }
            this.$store.commit(types.SET_DRAG_ITEM, {
                type: types.DRAG_ITEM_TYPE_TAB,
                startEvent: event,
                data: this.selection.items.length ? this.getSelectedItems(item) : item
            });
        },
        getSelectedItems (tab) {
            let items = [];
            if(this.selection.items.length){
                let window = this.items.find(w => w.id === this.selection.windowId);
                for(let i = 0; i < this.selection.items.length; i++){
                    let item = this.selection.items[i];
                    let index = window.tabs.findIndex(t => t.windowId === item.windowId && t.index === item.index);
                    if(index !== -1){
                        items.push(window.tabs[index]);
                    }
                }
            } else if(tab){
                items.push(tab);
            }
            return items;
        },
    }
}