import * as types from '../../store/mutation-types'
import brwsr from '../../services/browser'

export default {
    name: 'tabs-item',
    props: [
        'model',
        'selected'
    ],
    data () {
        return {
            // opened: true,
            noChildEvents: false,
            drop: {
                before: false,
                after: false
            }
        }
    },
    computed: {
        classes () {
            return {
                'list-item-active': this.model.active,
                'no-child-events': this.noChildEvents,
                'list-item-drop-before': this.drop.before,
                'list-item-drop-after': this.drop.after,
                'list-item-selected': this.selected,
            }
        },
        id () {
            return this.model.id;
        },
        dragItem () {
            return this.$store.state.dragItem;
        }
    },
    methods: {
        toggle (e) {
            if(e.ctrlKey || e.shiftKey) {
                this.select(e);
            } else {
                brwsr.updateTab(this.id, {active: true}).then((tab) => {
                    brwsr.updateWindow(tab.windowId, {focused: true})
                });
            }
        },
        select (e) {
            this.$emit('selectMe', {item: this.model, mouseEvent: e});
            e.preventDefault();
            e.stopPropagation();
        },
        closeTab () {
            brwsr.removeTabs(this.id);
            this.$emit('deselectItems');
        },
        itemDragStart (e) {
            this.$emit('dragStart', {item: this.model, event: e});
            // this.$store.commit(types.SET_DRAG_ITEM, {
            //     type: types.DRAG_ITEM_TYPE_TAB,
            //     startEvent: e,
            //     data: this.model
            // });
        },
        itemDragEnter (e) {
            if(e.target.classList.contains('list-item')){
                this.noChildEvents = true;
                switch (this.dragItem.type) {
                    case types.DRAG_ITEM_TYPE_TAB:
                        if(this.dragItem.data.id !== this.model.id){
                            if(this.dragItem.data.windowId !== this.model.windowId){
                                this.drop.before = true;
                            } else {
                                if(this.model.index < this.dragItem.data.index){
                                    this.drop.before = true;
                                } else {
                                    this.drop.after = true;
                                }
                            }
                        }
                        break;
                    case types.DRAG_ITEM_TYPE_BOOKMARK:
                    case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                        this.drop.before = true;
                        break;
                }
            }
        },
        itemDragOver () {

        },
        itemDragLeave (e) {
            if(e.target.classList.contains('list-item')){
                this.noChildEvents = false;
                this.drop.before = false;
                this.drop.after = false;
            }
        },
        itemDrop () {
            let items = Array.isArray(this.dragItem.data) ? this.dragItem.data : [this.dragItem.data];
            switch (this.dragItem.type) {
                case types.DRAG_ITEM_TYPE_TAB:
                    for(let i = 0; i < items.length; i++){
                        if(items[i].id !== this.model.id){
                            brwsr.moveTabs(items[i].id, {
                                windowId: this.model.windowId,
                                index: this.model.index
                            });
                        }
                    }
                    this.$emit('deselectItems');
                    break;
                case types.DRAG_ITEM_TYPE_BOOKMARK:
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    this.$emit('createTabs', {
                        items: items,
                        windowId: this.model.windowId,
                        index: this.model.index
                    });
                    break;
            }
            this.$store.commit(types.RESET_DRAG_ITEM);
            this.noChildEvents = false;
            this.drop.before = false;
            this.drop.after = false;
        }
    }
}