import * as types from '../../store/mutation-types'
import SpRecent from '../recent/recent.vue'
import SpTopsites from '../topsites/topsites.vue'
import SpApps from '../apps/apps.vue'
import SpTabs from '../tabs/tabs.vue'
import SpFoldersTree from '../bookmarks_tree/bookmarks_tree.vue'
export default {
    components: {
        SpRecent,
        SpTopsites,
        SpApps,
        SpTabs,
        SpFoldersTree
    },
    props: [
        'model'
    ],
    data () {
        return {
            loadColumn: false
        }
    },
    computed: {
        activeMenu () {
            return this.model.activeMenu; // apps, recent, topSites, tabs, folders
        },
        activeMenuTitle () {
            switch (this.activeMenu) {
                case 'tabs':
                    return 'Tabs';
                case 'topSites':
                    return 'Most visited';
                case 'recent':
                    return 'Recent tabs';
                case 'apps':
                    return 'Apps';
                case 'folders':
                    return 'Folders';
                default:
                    return ' ';
            }
        },
        activeComponent () {
            switch (this.activeMenu) {
                case 'tabs':
                    return 'SpTabs';
                case 'topSites':
                    return 'SpTopsites';
                case 'recent':
                    return 'SpRecent';
                case 'apps':
                    return 'SpApps';
                case 'folders':
                    return 'SpFoldersTree';
                default:
                    return 'SpTabs';
            }
        },
        width () {
            return this.model.width;
        },
        isLeftSide () {
            return this.model.position === 'left';
        },
        isRightSide () {
            return this.model.position === 'right';
        },
    },
    created () {
        if(this.model.display){
            this.loadColumn = true;
        }
    },
    methods: {
        switchMenu (menu) {
            this.$store.commit(types.COLUMN_CHANGE_ACTIVE_MENU, {columnId: this.model.id, menu: menu});
        },
        startResize (e) {
            window.document.addEventListener('mousemove', this.resize);
            window.document.addEventListener('mouseup', this.stopResize);
        },
        resize (e) {
            let width = Number(this.width); // TODO
            if(this.model.position === 'right'){
                if(width - e.movementX > 20 && width - e.movementX < 1000){
                    this.$store.commit(types.CHANGE_COLUMN_WIDTH, {columnId: this.model.id, width: width - e.movementX});
                }
            } else if (this.model.position === 'left') {
                if(width + e.movementX > 20 && width + e.movementX < 1000){
                    this.$store.commit(types.CHANGE_COLUMN_WIDTH, {columnId: this.model.id, width: width + e.movementX});
                }
            }
        },
        stopResize (e) {
            window.document.removeEventListener('mousemove', this.resize);
            window.document.removeEventListener('mouseup', this.stopResize);
        },
        toggleColumn () {
            if(!this.loadColumn){
                this.loadColumn = true;
            }
            this.$store.commit(types.COLUMN_TOGGLE, {columnId: this.model.id});
        },
        showContextMenu (e) {
            let menuBarItems = [
				{name: 'folders', title: 'Folders'},
                {name: 'tabs', title: 'Tabs'},
                {name: 'recent', title: 'Recent tabs'},
                {name: 'topSites', title: 'Most visited'},
                {name: 'apps', title: 'Apps'}
            ];
            let contextMenuItems = [];

            menuBarItems.forEach((item) => {
                contextMenuItems.push({
                    title: item.title,
                    menuName: item.name,
                    inactive: item.name === this.activeMenu,
                    vm: this,
                    clickFn: function (item, menu) {
                        item.vm.switchMenu(item.menuName);
                        menu.hide();
                    }
                });
            });

            this.$root.$emit('show-context-menu', {
                left: e.pageX,
                top: e.pageY,
                items: contextMenuItems
            });
        }
    }
}