import * as types from '../../store/mutation-types'
import brwsr from '../../services/browser'
import browser from "../../services/browser";

export default {
    props: [
        'browser'
    ],
    data () {
        return {
            items: [],
            tmpItems: [],
            openItemTimeout: null
        }
    },
    mounted () {
        this.setNaviItem(this.browser.currentFolderId);
        this.$root.$on('bookmarkChanged', (bookmark) => {
            this.items.forEach((item, index) => {
                if(item.id === bookmark.id){
                    this.items.splice(index, 1 , bookmark);
                }
            });
        });
        this.$root.$on('bookmarkMoved', (bookmarkId) => {
            this.items.forEach((item, index) => {
                if(item.id === bookmarkId){
                    this.rebuildNavi();
                }
            });
        });
    },
    watch: {
        'browser.currentFolderId': function (newVal, oldVal) {
            this.rebuildNavi(newVal);
        }
    },
    computed: {
        isSearch () {
            return this.browser.search.text;
        },
        rootFolderId () {
            return brwsr.BOOKMARKS_ROOT_FOLDER_ID;
        },
        dragItem () {
            return this.$store.state.dragItem;
        },
    },
    methods: {
        setNaviItem (folderId) {
            brwsr.getBookmark(folderId).then((bookmarks) => {
                if(bookmarks[0]){
                    this.tmpItems.unshift(bookmarks[0]);
                    if(bookmarks[0].hasOwnProperty('parentId')){
                        this.setNaviItem(bookmarks[0].parentId);
                    } else {
                        this.buildNavi();
                    }
                }
            }).catch(() => {
                console.log('Navi: Bookmark folder not found: ' + folderId);
            });
        },
        buildNavi () {
            if(this.tmpItems.length < this.items.length){
                this.items.splice(this.tmpItems.length-1, this.items.length - this.tmpItems.length);
            }
            this.tmpItems.forEach( (item, index, array) => {
                if(!this.items[index] || this.items[index].id !== item.id){
                    this.items.splice(index, 1, item);
                }
            });
        },
        rebuildNavi (folderId = this.browser.currentFolderId) {
            this.tmpItems = [];
            this.setNaviItem(folderId);
        },
        openItem (item) {
            this.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
                browserName: this.browser.name,
                folderId: item.id
            });
        },
        dragEnterItem (item) {
            switch (this.dragItem.type) {
                case types.DRAG_ITEM_TYPE_BOOKMARK:
                case types.DRAG_ITEM_TYPE_TAB:
                case types.DRAG_ITEM_TYPE_RECENT:
                case types.DRAG_ITEM_TYPE_TOP_SITE:
                case types.DRAG_ITEM_TYPE_BOOKMARK_FOLDER:
                    window.clearTimeout(this.openItemTimeout);
                    this.openItemTimeout = window.setTimeout(() => {
                        this.openItem(item);
                    }, 600);
                    break;
            }
        },
        dragLeaveItem () {
            window.clearTimeout(this.openItemTimeout);
        }
    }
}