import RecentItem from './recent_item.vue'
import DeviceItem from './device_item.vue'
import brwsr from '../../services/browser'

export default {
    components: {
        RecentItem,
        DeviceItem
    },
    data: function () {
        return {
            items: [],
            tmpHiddenItems: [],
            // storeTimeoutId: null,
            devices: [],
            devicesTest: [
                {
                    deviceName: 'Test H1',
                    sessions: [
                        {
                            window: {
                                sessionId: '444444',
                                tabs: [
                                    {sessionId: '1', title: 'tab1'},
                                    {sessionId: '2', title: 'tab2'},
                                    {sessionId: '3', title: 'tab3'},
                                    {sessionId: '4', title: 'tab4'}
                                ]
                            }
                        }
                    ]
                },
                {
                    deviceName: 'Test H2',
                    sessions: [
                        {
                            window: {
                                sessionId: '444444',
                                tabs: [
                                    {sessionId: '1', title: 'tab1'},
                                    {sessionId: '2', title: 'tab2'},
                                    {sessionId: '3', title: 'tab3'},
                                    {sessionId: '4', title: 'tab4'}
                                ]
                            }
                        }
                    ]
                }
            ]
        }
    },
    computed: {
        hiddenItems () {
            return this.$store.state.recentHiddenItems;
        }
    },
    mounted () {
        this.getRecentItems();
        brwsr.addListenerToSessions('onChanged', this.getRecentItems);
    },
    methods: {
        getRecentItems () {
            this.$store.dispatch('restoreHiddenItems').then( () => {
                brwsr.getRecentlyClosed().then((sessions) => {
                    this.items = sessions;
                    brwsr.getDevices().then((devices) => {
                        // uncomment for devices UI test
                        // this.devices = this.devicesTest;
                        // devices = this.devicesTest;

                        this.devices = devices; // comment for devices UI test
                        for(let z=0; z < devices.length; z++){
                            sessions = sessions.concat(devices[z].sessions);
                        }
                        window.setTimeout( () => {
                            for(let i=0; i < sessions.length; i++){
                                if(sessions[i].window){
                                    this.checkOldHiddenItems(sessions[i], sessions[i].window.sessionId);
                                } else {
                                    this.checkOldHiddenItems(sessions[i]);
                                }
                            }
                        }, 1000);
                    });
                });
            });
        },
        checkOldHiddenItems (session, windowSessionId) {
            if(!session.window){
                let id = windowSessionId ? windowSessionId + '_' + session.sessionId : session.tab.sessionId + '_' + session.lastModified;
                if(this.hiddenItems.indexOf(id) > -1){
                    this.tmpHiddenItems.push(id);
                }
                this.$store.dispatch('saveCheckedHiddenRecentItems', this.tmpHiddenItems); // TODO to types.*
            } else {
                for(let i=0; i < session.window.tabs.length; i++){
                    this.checkOldHiddenItems(session.window.tabs[i], session.window.sessionId);
                }
            }
        }
    }
}