import * as types from '../../store/mutation-types'
import BookmarksBrowser from '../bookmarks_browser/bookmarks_browser.vue'
import SideColumn from '../side_column/side_column.vue'
import ContextMenu from '../context_menu/context_menu.vue'
import EditDialog from '../edit_dialog/edit_dialog.vue'
import SearchBar from '../search/search_bar.vue'
import ColorPicker from '../color_picker/color_picker.vue'
import FoldersBar from '../folders_bar/menu_bar.vue'
import WhatIsNew from '../what_is_new/what_is_new.vue'
import OverridesManager from '../overrides_manager/overrides_manager.vue'
// import DragItem from '../drag_item/drag_item.vue'
import NewSettings from '../new_settings/settings.vue'
import NewBackground from '../new_background/background.vue'
import brwsr from '../../services/browser'

export default {
  components: {
    SideColumn,
    BookmarksBrowser,
    ContextMenu,
    EditDialog,
    SearchBar,
    ColorPicker,
    NewSettings,
    NewBackground,
    FoldersBar,
    WhatIsNew,
    OverridesManager,
    // DragItem
  },
  data () {
    return {
      visibility: 'hidden'
    }
  },
  computed: {
    classes () {
      return {
        [this.theme + '-theme']: this.theme,
      }
    },
    styles () {
      return {
        'width': '100%',
        'height': '100%',
        'visibility': this.visibility, // start anim test TODO
        'color': this.decor.text.color.length ? 'rgba('+ this.decor.text.color +')' : ''
      }
    },
    theme () {
      // return this.$store.state.theme;
      return this.$store.state.decor.theme.name;
    },
    decor () {
      return this.$store.state.decor;
    },
    leftColumnModel () {
      return this.$store.state.columns.left;
    },
    rightColumnModel () {
      return this.$store.state.columns.right;
    },
    quickStartFolder () {
      return this.$store.state.quickStartFolder;
    },
    availableNewSettings () {
      return this.$store.state.settings.newSettingsAvailable;
    },
    isTopbarOn () {
      return this.$store.state.showTopBar;
    },
    contentHeight () {
      if(!this.isTopbarOn){
        return 'calc(100% - ' + (this.$store.state.toggleTopBarHeight + 10) + 'px';
      }
      return 'calc(100% - '+ (this.$store.state.topBarHeight + this.$store.state.toggleTopBarHeight + 10) + 'px)';
    },
    // currentBookmarkBrowser () {
    //   return this.$store.state.browsers.find(b => b.name === this.$store.state.activeBrowser) || this.$store.state.browsers[0];
    // }
  },
  created () {
    window.setTimeout(() => {this.visibility = 'visible'}, 300); // start anim test TODO
    this.$store.dispatch(types.ACTION_RESTORE_DECOR);
    this.$store.commit(types.RESTORE_CONFIG);
    this.$store.commit(types.RESTORE_FOLDERS_CONFIG);
    this.$store.commit(types.RESTORE_TOPBAR_STATE);
    this.$store.commit(types.RESTORE_COLUMNS_STATE);
    this.$store.commit(types.RESTORE_BOOKMARKS_BROWSERS);
    this.$store.commit(types.RESTORE_ACTIVE_BOOKMARKS_BROWSER);
    this.$store.dispatch(types.ACTION_RESTORE_OVERRIDDEN_ITEMS);
    this.checkQuickStartFolder();
    window.setTimeout(() => {
            this.$store.commit(types.LOAD_NEW_SETTINGS_AVAILABLE);
            this.$store.commit(types.LOAD_WHAT_IS_NEW, brwsr.getManifest().version);

            window.setConfig2 = (val) => {
                this.$store.commit(types.CHANGE_CONFIG, {
                    prop: 'useVisibleOptionForHideColumnTopBar',
                    value: val
                });
            } // TODO del
            window.setCompactViewLineSpacingSize = (val) => {
              this.$store.commit(types.SET_DECOR_PROP, {prop: 'text.compactViewLineSpacingSize', value: val});
            } // TODO del
    }, 5000);
    window.addEventListener('blur', () => {
        this.$store.commit(types.SET_ACTIVE_SPACE, '');
        this.resetDragItem();
    });
    window.addEventListener('click', this.resetDragItem);
  },
  mounted () {
    // Chrome events
    brwsr.addListenerToBookmarks('onChildrenReordered', (id, reorderInfo) => {
      // never happened -_-
      console.log('Reordered');
    });
    brwsr.addListenerToBookmarks('onMoved', (id, moveInfo) => {
      brwsr.getBookmark(id).then((bookmarks) => {
        if(bookmarks.length > 0){
          console.log('Bookmark Moved');
          this.$root.$emit('bookmarkMoved', id, moveInfo, bookmarks[0]);
        }
      });
    });
    brwsr.addListenerToBookmarks('onCreated', (id, bookmark) => {
      console.log('Bookmark Created');
      this.$root.$emit('bookmarkCreated', bookmark);
    });
    brwsr.addListenerToBookmarks('onRemoved', (id, removeInfo) => {
      console.log('Bookmark Removed');
      this.$root.$emit('bookmarkRemoved', id, removeInfo);
      this.$store.commit(types.DELETE_FOLDERS_CONFIG, id);
    });
    brwsr.addListenerToBookmarks('onChanged', (id, changeInfo) => {
      brwsr.getBookmark(id).then((bookmarks) => {
        if(bookmarks.length > 0){
          console.log('Bookmark Changed');
          this.$root.$emit('bookmarkChanged', bookmarks[0]);
        }
      });
    });
    brwsr.addListenerToCommands('onCommand', (command) => {
      if(command === 'toggle-quick-start'){
        this.$store.commit(types.SET_ACTIVE_BOOKMARKS_BROWSER,
            this.$store.state.browsers[this.$store.state.activeBrowser === 'b1' ? 1 : 0].name
        );
      }
    });
    brwsr.addListenerToRuntime('onMessage', (msg, sender)=>{
      if(msg.text === types.MSG_BROWSERS_CHANGED){
        this.$store.commit(types.RESTORE_BOOKMARKS_BROWSERS);
      }
    });
  },
  methods: {
    openSettings () {
      // if(!this.$store.state.settings.load){
      //   this.$store.commit(types.LOAD_SETTINGS);
      // }
      this.$store.commit(types.SET_ACTIVE_SPACE, '');
      this.$store.commit(types.SHOW_SETTINGS);
    },
    checkQuickStartFolder () {
      if(window.localStorage.hasOwnProperty(types.STORAGE_KEY_QUICK_START_FOLDER_ID)){
        const quickStartFolderId = window.localStorage[types.STORAGE_KEY_QUICK_START_FOLDER_ID];
        brwsr.getBookmark(quickStartFolderId).then((bookmarks) => {
          if(brwsr.isBookmarkFolder(bookmarks[0])){
            this.commitQuickStartFolder(bookmarks[0]);
          } else {
            this.createQuickStartFolder();
          }
        }).catch(() => {
          this.createQuickStartFolder();
        });
      } else {
        this.createQuickStartFolder();
      }
    },
    createQuickStartFolder () {
      brwsr.searchBookmarks(types.QUICK_START_FOLDER_NAME).then((bookmarks) => {
        if(brwsr.isBookmarkFolder(bookmarks[0])){
          this.commitQuickStartFolder(bookmarks[0]);
        } else {
          brwsr.createBookmark({
            title: types.QUICK_START_FOLDER_NAME
          }).then((folder) => {
            this.commitQuickStartFolder(folder);
            brwsr.getTopSites().then((sites) => {
              for(let i=0; i < sites.length && i <= 9; i++){
                brwsr.createBookmark({
                  parentId: folder.id,
                  title: sites[i].title,
                  url: sites[i].url
                });
              }
            });
          });
        }
      });
    },
    commitQuickStartFolder (bookmark) {
      this.$store.commit(types.SET_QUICK_START_FOLDER, bookmark);
      this.$store.commit(types.BB_SET_TOP_LEVEL_ID, {
        browserName: this.$store.state.browsers[0].name,
        bookmarkId: bookmark.id
      });
      this.$store.commit(types.BB_CHANGE_CURRENT_FOLDER, {
        browserName: this.$store.state.browsers[0].name,
        folderId: bookmark.id
      });
    },
    setActiveBrowserSpace () {
      this.$store.commit(types.SET_ACTIVE_SPACE_BY_BROWSER_NAME, this.$store.state.activeBrowser);
    },
    toggleTopbar () {
      this.$store.commit(types.TOPBAR_TOGGLE);
    },
    resetDragItem () {
      if(this.$store.state.dragItem.type){
        window.setTimeout(() => {
          this.$store.commit(types.RESET_DRAG_ITEM);
        }, 100);
      }
    }
  }}