import * as types from "../../store/mutation-types";

export default {
	props: [
		'display',
		'tileTitles',
		'icons',
		'tiles'
	],
	data () {
		return {
			menu: types.OVERRIDDEN_ITEM_TYPE_ICON,
			types: {
				icons: types.OVERRIDDEN_ITEM_TYPE_ICON,
				tiles: types.OVERRIDDEN_ITEM_TYPE_TILE,
				tileTitles: types.OVERRIDDEN_ITEM_TYPE_TILE_TITLE
			},
			importNotice: false,
			importError: false,
			importedResult: {
				icons: 0,
				tiles: 0
			},
			tag: 'quickStartpageOverridesJson'

		}
	},
	computed: {
		isIconsActive () {
			return this.menu === this.types.icons;
		},
		isTilesActive () {
			return this.menu === this.types.tiles;
		},
		exportDataLink () {
			return 'data:text/json;charset=utf-8,' + encodeURIComponent(JSON.stringify({
				[this.tag]: true,
				icons: this.icons,
				tiles: this.tiles,
				tileTitles: this.tileTitles
			}))
		}
	},
	methods: {
		deleteItem (bookmarkUrl, type) {
			this.$store.commit(types.DELETE_OVERRIDDEN_ITEM, {type: type, bookmarkUrl: bookmarkUrl});
		},
		importOverrides (e) {
			this.importNotice = false;
			this.importError = false;
			let file = new FileReader();
			file.addEventListener('load', () => {
				try {
					const data = JSON.parse(file.result);
					if(data[this.tag]){
						this.importedResult.icons = Object.keys(data.icons).length;
						this.importedResult.tiles = Object.keys(data.tiles).length;
						this.importNotice = true;
						this.$store.commit(types.MERGE_OVERRIDES, {type: types.OVERRIDDEN_ITEM_TYPE_ICON, overrides: data.icons});
						this.$store.commit(types.MERGE_OVERRIDES, {type: types.OVERRIDDEN_ITEM_TYPE_TILE, overrides: data.tiles});
						this.$store.commit(types.MERGE_OVERRIDES, {type: types.OVERRIDDEN_ITEM_TYPE_TILE_TITLE, overrides: data.tileTitles});
					} else {
						this.importError = true;
					}
				} catch (err) {
					console.log(err);
					this.importError = true;
				}
			}, false);
			file.readAsText(e.target.files[0]);
		},
		closeManager () {
			this.$store.commit(types.CLOSE_OVERRIDES_MANAGER);
		}
	}
}