import * as types from './mutation-types'
import brwsr from '../services/browser'
import bgImages from "./_images";
import bgPatterns from "./_patterns";

let decorStorageTimer,
    recentItemsStorageTimer;

const storagePlugin = store => {	
    store.subscribe((mutation, state) => {
        switch (mutation.type) {
            case types.CHANGE_CONFIG:
                window.localStorage.setItem(types.STORAGE_KEY_CONFIG, JSON.stringify(state.config));
                break;

            case types.SET_FOLDERS_CONFIG:
            case types.DELETE_FOLDERS_CONFIG:
            case types.DELETE_FOLDERS_CONFIG_PROP:
                window.localStorage.setItem(types.STORAGE_KEY_FOLDERS_CONFIG, JSON.stringify(state.foldersConfig));
                break;

            case types.SHOW_SETTINGS:
                window.localStorage.setItem(types.STORAGE_KEY_SETTINGS_VERSION, state.settings.version);
                break;

            case types.COLUMN_CHANGE_ACTIVE_MENU:
            case types.COLUMN_TOGGLE:
            case types.CHANGE_COLUMN_WIDTH:
                window.localStorage.setItem(types.STORAGE_KEY_COLUMNS, JSON.stringify(state.columns));
                break;

            case types.TOPBAR_TOGGLE:
                window.localStorage.setItem(types.STORAGE_KEY_TOPBAR_DISPLAY, state.showTopBar);
                break;

            case types.SET_ACTIVE_BOOKMARKS_BROWSER:
                window.localStorage.setItem(types.STORAGE_KEY_ACTIVE_BROWSER, state.activeBrowser);
                break;
            case types.BB_CHANGE_VIEW:
            case types.BB_SET_TITLE:
            case types.BB_CHANGE_CURRENT_FOLDER:
            case types.ADD_NEW_BB:
            case types.REMOVE_BB:
            case types.BB_FOLDER_BAR_MOVE_ITEM:
            case types.SET_QUICK_START_FOLDER:
                if(window.localStorage.getItem(types.STORAGE_KEY_QUICK_START_FOLDER_ID) !== state.quickStartFolder.id){
                    window.localStorage.setItem(types.STORAGE_KEY_QUICK_START_FOLDER_ID, state.quickStartFolder.id);
                }
                window.localStorage.setItem(types.STORAGE_KEY_BROWSERS, JSON.stringify(state.browsers));
                break;

            case types.TREE_CHANGE_FOLDER_STATE:
                window.localStorage.setItem(types.STORAGE_KEY_TREE_OPENED_FOLDERS, JSON.stringify(state.openedFolders));
                break;

            case types.SET_BACKGROUND:
            case types.SET_DECOR_PROP:
            case types.SET_UNSPLASH_PHOTOS:
                window.clearTimeout(decorStorageTimer);
                decorStorageTimer = window.setTimeout(() => {
                    brwsr.setStorageData('local', {
                        [types.STORAGE_KEY_DECOR]: JSON.stringify({
                            adaptationToBackgroundPic: state.decor.adaptationToBackgroundPic,
                            theme: {
                                name: state.decor.theme.name
                            },
                            background: {
                                autoChange: state.decor.background.autoChange,
                                autoChangeModify: state.decor.background.autoChangeModify,
                                id: state.decor.background.id,
                                url: state.decor.background.url,
                                author: state.decor.background.author,
                                type: state.decor.background.type, // need?
                                opacity: state.decor.background.opacity,
                                gamma: state.decor.background.gamma,
                                color: state.decor.background.color,
                                effect: {
                                    blend: state.decor.background.effect.blend,
                                    color: state.decor.background.effect.color,
                                },
                                sets: {
                                    unsplash: {
                                        photos: state.decor.background.sets.unsplash.photos,
                                        modify: state.decor.background.sets.unsplash.modify
                                    }
                                }
                            },
                            text: {
                                color: state.decor.text.color,
                                compactViewLineSpacingSize: state.decor.text.compactViewLineSpacingSize
                            },
                            tile: {
                                backgroundColor: state.decor.tile.backgroundColor,
                                borderColor: state.decor.tile.borderColor
                            },
                        })
                    });
                }, 150);
                break;

            case types.ADD_TO_RECENT_HIDDEN_ITEMS:
                if(parseInt(recentItemsStorageTimer) >= 0){
                    window.clearTimeout(recentItemsStorageTimer);
                }
                recentItemsStorageTimer = window.setTimeout( () => {
                    brwsr.setStorageData('sync', {
                        [types.STORAGE_KEY_RECENT_HIDDEN_ITEMS]: state.recentHiddenItems
                    });
                }, 1000);
                break;

            // case types.SET_UNSPLASH_PHOTOS:
                // window.localStorage.setItem(types.STORAGE_KEY_UNSPLASH_PHOTOS, JSON.stringify(state.decor.background.sets.unsplash.photos));
                // window.localStorage.setItem(types.STORAGE_KEY_UNSPLASH_MODIFY_TIME, state.decor.background.sets.unsplash.modify);
                // break; // TODO del ?

            case types.LOAD_WHAT_IS_NEW:
                if(state.whatIsNewAvailable){
                    window.localStorage.setItem(types.STORAGE_KEY_WHAT_IS_NEW_VERSION, mutation.payload);
                }
                break;

            case types.CHANGE_SEARCH_ENGINE:
                window.localStorage.setItem(types.STORAGE_SEARCH_ENGINE_ID, state.searchEngineId);
                break;

            case types.DELETE_OVERRIDDEN_ITEM:
            case types.MERGE_OVERRIDES:
            case types.SET_OVERRIDE_ITEM:
                brwsr.setStorageData('local', {
                    [types.STORAGE_ICON_OVERRIDES]: JSON.stringify(state.iconOverrides),
                    [types.STORAGE_TILE_PIC_OVERRIDES]: JSON.stringify(state.tilePicOverrides),
                    [types.STORAGE_TILE_TITLE_OVERRIDES]: JSON.stringify(state.tileTitleOverrides)
                });
                break;
        }
    })
};

const debugPlugin = store => {
    store.subscribe((mutation, state) => {
        console.log(':: ', mutation, state);
    })
};

export default [/*debugPlugin,*/ storagePlugin];

