
const clientId = 'e599b76fc8463cbbe2e37a458ca4cdd44760852fd7fa2379fca304205359b31a';
const apiUrl = 'https://api.unsplash.com/';
// const apiUrl = 'https://api.unsplash.com/photos?client_id=' + clientId;

export default {
    // https://unsplash.com/documentation#list-photos
    getPhotos(page, perPage, orderBy) {
        return new Promise ((resolve, reject) => {
            const xhr = new XMLHttpRequest(),
                method = 'GET',
                url = apiUrl + 'photos?client_id=' + clientId +
                    '&page=' + (page || '') +
                    '&per_page=' + (perPage || '') +
                    '&order_by=' + (orderBy || '');

            xhr.open(method, url, true);
            xhr.onreadystatechange = function () {
                // In local files, status is 0 upon success in Mozilla Firefox
                if(xhr.readyState === XMLHttpRequest.DONE) {
                    let status = xhr.status;
                    if (status === 0 || (status >= 200 && status < 400)) {
                        // The request has been completed successfully
                        resolve(xhr.responseText ? JSON.parse(xhr.responseText) : []);
                    } else {
                        // Oh no! There has been an error with the request!
                        reject(xhr);
                    }
                }
            };
            xhr.send();
        });
    },
    // https://unsplash.com/documentation#get-a-random-photo
    getRandomPhoto ({count = 1, orientation = 'landscape', content_filter = 'high'}) {
        return new Promise ((resolve, reject) => {
            const xhr = new XMLHttpRequest(),
                method = 'GET',
                url = apiUrl + 'photos/random?client_id=' + clientId +
                    '&count=' + count +
                    '&orientation=' + orientation +
                    '&content_filter=' + content_filter;

            xhr.open(method, url, true);
            xhr.onreadystatechange = function () {
                if(xhr.readyState === XMLHttpRequest.DONE) {
                    let status = xhr.status;
                    if (status === 0 || (status >= 200 && status < 400)) {
                        resolve(xhr.responseText ? JSON.parse(xhr.responseText) : []);
                    } else {
                        reject(xhr);
                    }
                }
            };
            xhr.send();
        });
    },

}