export default {
	data () {
		return {
			display: false,
			hue: 0,
			saturate: 0,
			value: 0,
			opacity: 0.75,
			gradientRect: {},
			prop: '',
			changeFn: null,
			prevColor: [],
			mouseOutOfGradient: false,
			pickerCaught: false,
			palette: [
				[0,0,0,1],
				[255,255,255,1],
				[63,63,63,1],
				[89,89,89,1],
				[127,127,127,1],
				[165,165,165,1],
				[191,191,191,1],
				[216,216,216,1],
				[242,242,242,1],
				[147,137,83,1],
				[196,189,151,1],
				[221,217,195,1],
				[84,141,212,1],
				[141,179,226,1],
				[198,217,240,1],
				[217,150,148,1],
				[229,185,183,1],
				[242,220,219,1],
				[195,214,155,1],
				[215,227,188,1],
				[235,241,221,1],
				[178,162,199,1],
				[204,193,217,1],
				[229,224,236,1],
				[146,205,220,1],
				[183,221,232,1],
				[219,238,243,1],
				[250,192,143,1],
				[251,213,181,1],
				[253,234,218,1]
			]
		}
	},
	computed: {
		color: {
			cache: false,
			get: function () {
				let color = this.HSVtoRGB(this.hue, this.saturate, this.value);
				return 'rgba('+ color.r +',' +color.g+','+ color.b +','+ this.opacity+')';
			}
		},
		colorRaw: {
			cache: false,
			get: function () {
				let color = this.HSVtoRGB(this.hue, this.saturate, this.value);
				return [color.r, color.g, color.b, this.opacity];
			}
		},
		rainbowColor: {
			cache: false,
			get: function () {
				let color = this.HSVtoRGB(this.hue, 1, 1);
				return [color.r, color.g, color.b, 1];
			}
		},
		gradientElm () {
			return this.$el.querySelector('.color-pick-gradient');
		},
		pointerX () {
			return this.gradientRect.width * this.saturate;
		},
		pointerY () {
			return this.gradientRect.height * (1 - this.value);
		}
	},
	mounted () {
		this.$watch('colorRaw', (newVal, oldVal) => {
			if(typeof this.changeFn == 'function'){
				newVal[3] = parseFloat(newVal[3]);
				this.changeFn (this.prop, newVal);
			}
		});
		// data.left .top .startColor .prop .changeFn
		this.$root.$on('show-color-picker', (data) => {
			this.prevColor = data.startColor;
			this.show(data);			
		});		
	},
	methods: {
		// data.left .top .startColor .prop .changeFn
		show (data) {
			if(this.display){
				this.hide();
			}
			this.display = true;
			this.$nextTick( () => {
				this.gradientRect = this.gradientElm.getBoundingClientRect();
				this.changeFn = data.changeFn;
				this.prop = data.prop;
				this.setColor(data.startColor.length ? data.startColor : [156,156,156,1]);
				const picker = this.$el,
					pickerRect = picker.getBoundingClientRect();
				// var pickerRect = picker.getBoundingClientRect();
				// console.log('Picker', pickerRect, pickerRect.width, pickerRect.height);
				if(data.left + pickerRect.width > window.innerWidth){
					data.left -= pickerRect.width;
				}
				// if(data.top + pickerRect.height > window.innerHeight){
				// 	data.top -= pickerRect.height;
				// }
				data.top -= pickerRect.height;
				// picker.style.left = data.left - pickerRect.width/2 +'px';
				// picker.style.left = (data.left - 13) +'px';
				picker.style.left = data.left +'px';
				picker.style.top = (data.top - 5) +'px';
			});
			window.document.body.addEventListener('click', this.hide);
		},
		hide () {
			if(!this.pickerCaught){
				this.display = false;
				this.prop = '';
				this.changeFn = '';
				this.prevColor = '';
				this.hue = 0;
				this.saturate = 0;
				this.value = 0;
				this.opacity = 0.75;
				window.document.body.removeEventListener('click', this.hide);
			}
		},
		cancel () {
			if(typeof this.changeFn === 'function'){
				this.changeFn (this.prop, this.prevColor);
			}
			this.hide();
		},
		// reset () {
		// 	// this.obj[this.prop] = '';
		// 	// this.hide();
		// 	// TODO
		// },
		setColor (rgbColor) {
			if(Array.isArray(rgbColor) && rgbColor.length){
				const hsvColor = this.RGBtoHSV(rgbColor[0], rgbColor[1], rgbColor[2]);
				this.hue = hsvColor.h;
				this.saturate = hsvColor.s;
				this.value = hsvColor.v;
				this.opacity = rgbColor[3] || 1;
			} else {
				const rgb = this.HSVtoRGB(this.hue, this.saturate, this.value);
				this.setColor([rgb.r, rgb.g, rgb.b, this.opacity]);
			}
		},
		colorPointerCatch (e) {
			e.stopPropagation();
			e.preventDefault();
			this.pickerCaught = true;
			this.$nextTick( () => {
				this.pickerRect = this.$el.getBoundingClientRect();
				this.gradientRect = this.gradientElm.getBoundingClientRect();
				window.document.body.addEventListener('mousemove', this.colorPointerMove);
				window.document.body.addEventListener('mouseup', this.colorPointerRelease);
			});
		},
		colorPointerMove (e) {
			if(e.pageX < this.gradientRect.left){
				this.saturate = 0;
			}
			if(e.pageX > this.gradientRect.right){
				this.saturate = 1;
			}
			if(e.pageX >= this.gradientRect.left && e.pageX <= this.gradientRect.right){
				this.saturate = (e.pageX - this.gradientRect.left) / this.gradientRect.width ;
			}
			if(e.pageY < this.gradientRect.top){
				this.value = 1;
			}
			if(e.pageY > this.gradientRect.bottom){
				this.value = 0;
			}
			if(e.pageY >= this.gradientRect.top && e.pageY <= this.gradientRect.bottom){
				this.value = (this.gradientRect.height - (e.pageY - this.gradientRect.top)) / this.gradientRect.height;
			}
		},
		colorPointerRelease (e) {
			e.stopPropagation();
			e.preventDefault();
			window.setTimeout( () => {
				this.pickerCaught = false;
			}, 30); // TODO -_-
			window.document.body.removeEventListener('mousemove', this.colorPointerMove);
			window.document.body.removeEventListener('mouseup', this.colorPointerRelease);
		},
		HSVtoRGB (h, s, v) {
			var r, g, b, i, f, p, q, t;
			if (arguments.length === 1) {
				s = h.s, v = h.v, h = h.h;
			}
			i = Math.floor(h * 6);
			f = h * 6 - i;
			p = v * (1 - s);
			q = v * (1 - f * s);
			t = v * (1 - (1 - f) * s);

			switch (i % 6) {
				case 0: r = v, g = t, b = p; break;
				case 1: r = q, g = v, b = p; break;
				case 2: r = p, g = v, b = t; break;
				case 3: r = p, g = q, b = v; break;
				case 4: r = t, g = p, b = v; break;
				case 5: r = v, g = p, b = q; break;
			}
			return {
				r: Math.round(r * 255),
				g: Math.round(g * 255),
				b: Math.round(b * 255)
			};
		}, // http://stackoverflow.com/questions/17242144/javascript-convert-hsb-hsv-color-to-rgb-accurately
		RGBtoHSV (r, g, b) {
			if (arguments.length === 1) {
				g = r.g, b = r.b, r = r.r;
			}
			var max = Math.max(r, g, b), min = Math.min(r, g, b),
				d = max - min,
				h,
				s = (max === 0 ? 0 : d / max),
				v = max / 255;

			switch (max) {
				case min: h = 0; break;
				case r: h = (g - b) + d * (g < b ? 6: 0); h /= 6 * d; break;
				case g: h = (b - r) + d * 2; h /= 6 * d; break;
				case b: h = (r - g) + d * 4; h /= 6 * d; break;
			}

			return {
				h: h,
				s: s,
				v: v
			};
		}
	}
}