import brwsr from '../../services/browser'

export default {
	data () {
		return {
			items: []
		}
	},
	mounted () {
		this.getItems();
	},
	methods: {
		getItems () {
			brwsr.getExtensions().then((extensions) => {
				let apps = [];
				for(let i=0; i < extensions.length; i++){
					if(extensions[i].isApp){
						apps.push(extensions[i]);
					}
				}
				this.items = apps;
			});
		},
		launch (item) {
			brwsr.launchApp(item.id);
		}
	}
}