import * as types from '../../store/mutation-types'
import brwsr from '../../services/browser'
import unsplash from '../../services/unsplash'
import colorHelper from '../../helpers/color'

export default {
    name: 'settings',
    props: [
        'decor',
        'display'
    ],
    components: {

    },
    data () {
        return {
            backgroundChooseActiveMenu: 'image', // 'image', 'pattern', 'color',
            inputUrl: '',
            types: {
                picture: 'image',
                pattern: 'pattern',
                color: 'color',
                lightThemeName: types.LIGHT_THEME_NAME,
                darkThemeName: types.DARK_THEME_NAME,
            },
            loadingBackgroundPicture: null,
            loadingFromUrl: false,
            updateUnsplashTimer: 24, // hours
            updateAutoBackgroundPicTimer: 12, // hours
        }
    },
    mounted () {
        window.setTimeout(() => {
            if(this.isDailyPicturesExpired){
                this.getDailyPictures();
            }
            this.setRandomPictureFromUnsplash();
            window.setInterval(this.setRandomPictureFromUnsplash, 3600*1000);
        }, 2000);
    },
    computed: {
        backgrounds () {
            switch (this.backgroundChooseActiveMenu) {
                case this.types.picture:
                    return this.decor.background.sets.pictures;
                case this.types.pattern:
                    return this.decor.background.sets.patterns;
                case this.types.color:
                    return this.decor.background.sets.colors;
                default:
                    return [];
            }
        },
        isDailyPicturesExpired () {
            return this.decor.background.sets.unsplash.modify ?
                (Date.now() - this.decor.background.sets.unsplash.modify) >= 3600*1000*this.updateUnsplashTimer : true;
        },
        dailyPictures() {
            return this.decor.background.sets.unsplash.photos;
        },
        isPicturesMenuActive () {
            return this.backgroundChooseActiveMenu === this.types.picture;
        },
        isLightTheme () {
            return this.decor.theme.name === types.LIGHT_THEME_NAME;
        },
        isDarkTheme () {
            return this.decor.theme.name === types.DARK_THEME_NAME;
        },
        isPictureGammaDark () {
            return this.decor.background.gamma >= 0 && this.decor.background.gamma < 1;
        },
        inputLinkPlaceholderText () {
            return this.isPicturesMenuActive ? 'Paste picture link or choose file' : 'Paste pattern link or choose file';
        },
        bgTuningTitle () {
            return this.isPicturesMenuActive ? 'Picture tuning' : 'Pattern tuning';
        },
        resetBackgroundPicture () {
            return this.decor.background.url ? 'linear-gradient(to bottom, rgba(255,255,255,0.7) 0%,rgba(255,255,255,0.7) 100%), no-repeat center/auto 60px url('+ this.decor.background.url +')' : ''
        },
        autoTheme: {
            get () {
                return this.decor.adaptationToBackgroundPic;
            },
            set (value) {
                this.$store.commit(types.SET_DECOR_PROP, {
                    prop: 'adaptationToBackgroundPic',
                    value: value
                });
                if(value){
                    this.detectTheme();
                }
            }
        },
        backgroundOpacity: {
            get () {
                return this.decor.background.opacity;
            },
            set (value) {
                this.$store.commit(types.SET_DECOR_PROP, {prop: 'background.opacity', value: value});
            }
        },
        backgroundEffect: {
            get () {
                return this.decor.background.effect.blend;
            },
            set (value) {
                this.$store.commit(types.SET_DECOR_PROP, {prop: 'background.effect.blend', value: value});
            }
        },
        credits () {
            return 'Quick startpage ' + brwsr.getManifest().version + ' - timg.feedback@gmail.com';
        }
    },
    watch: {
        inputUrl: function (url) {
            if(url){
                try {
                    new URL(url);
                    this.loadingFromUrl = true;
                    this.loadImage({
                        url: url,
                        type: this.backgroundChooseActiveMenu,
                    });
                } catch (e) {
                    console.log('Wrong picture url');
                } // https://stackoverflow.com/questions/5717093/check-if-a-javascript-string-is-a-url
            }
        },
    },
    methods: {
        hide () {
            this.$store.commit(types.HIDE_SETTINGS);
        },
        setTheme (name) {
            this.$store.commit(types.SET_DECOR_PROP, {prop: 'theme.name', value: name});
        },
        detectTheme () {
            this.$store.commit(types.SET_DECOR_PROP, {
                prop: 'theme.name', value: this.isPictureGammaDark ? types.DARK_THEME_NAME : types.LIGHT_THEME_NAME
            });
        },
        setBackgroundChooseActiveMenu (menuName) {
            this.backgroundChooseActiveMenu = menuName;
        },
        isBackgroundChooseMenuActive (menuName) {
            return this.backgroundChooseActiveMenu === menuName;
        },
        isBackgroundPictureLoading (bgItem) {
            return bgItem === this.loadingBackgroundPicture;
        },
        setBackground (item) {
            if(item) {
                this.loadingBackgroundPicture = item;
                switch (item.type) {
                    case this.types.picture:
                    case this.types.pattern:
                        this.loadImage(item);
                        break;
                    default:
                        if(Array.isArray(item)){
                            this.$store.commit(types.SET_BACKGROUND, {
                                type: this.types.color,
                                color: item
                            });
                        } // TODO Used for colors sets, remove?
                }
            } else {
                this.$store.commit(types.SET_BACKGROUND); // reset background
            }
        },
        getDailyPictures () {
            unsplash.getRandomPhoto({count: 30}).then((photos) => {
                let selectedPhotos = [];
                photos.forEach((item) => {
                    if(item.height/item.width <= 1.2){
                        selectedPhotos.push(this.parseUnsplashPhoto(item));
                    }
                });
                if(photos.length){
                    this.$store.commit(types.SET_UNSPLASH_PHOTOS, {photos: selectedPhotos, modify: Date.now()});
                }
            }).catch();
        },
        parseUnsplashPhoto (item) {
            return {
                type: this.types.picture,
                url: item.urls.full,
                tmb: item.urls.thumb,
                author: {
                    link: item.links.html,
                    user: {
                        firstName: item.user.first_name,
                        lastName: item.user.last_name,
                        html: item.user.links.html
                    }
                }
            }
        },
        setRandomPictureFromUnsplash () {
            if(this.decor.background.autoChange && (Date.now() - this.decor.background.autoChangeModify) >= 3600*1000*this.updateAutoBackgroundPicTimer){
                unsplash.getRandomPhoto({}).then((photos) => {
                    this.loadImage(this.parseUnsplashPhoto(photos[0]));
                    this.$store.commit(types.SET_DECOR_PROP, {prop: 'background.autoChangeModify', value: Date.now()});
                }).catch();
            }
        },
        toggleAutoChangeBackground () {
            this.$store.commit(types.SET_DECOR_PROP, {prop: 'background.autoChange', value: !this.decor.background.autoChange});
            this.$store.commit(types.SET_DECOR_PROP, {prop: 'background.autoChangeModify', value: this.decor.background.autoChangeModify || Date.now()});
            this.setRandomPictureFromUnsplash();
        },
        // https://stackoverflow.com/questions/19262141/resize-image-with-javascript-canvas-smoothly#19262385
        // https://stackoverflow.com/questions/6150289/how-to-convert-image-into-base64-string-using-javascript
        loadImage (item) {
            let canvas = document.createElement('canvas');
            let ctx = canvas.getContext('2d');
            let img = new Image();
            img.crossOrigin = "Anonymous"; // https://developer.mozilla.org/en-US/docs/Web/HTML/CORS_enabled_image
            img.onload = () => {
                if(item.type === types.BACKGROUND_TYPE_IMAGE){
                    canvas.width = window.screen.width; // destination canvas size
                    canvas.height = canvas.width * img.height / img.width;
                } else {
                    canvas.width = img.width;
                    canvas.height = img.height;
                }
                ctx.imageSmoothingQuality = 'high';
                ctx.imageSmoothingEnabled = true;
                ctx.drawImage(img, 0, 0, canvas.width, canvas.height);

                // console.log('Settings.loadImage', colorHelper.getImageColorState(img));
                this.$store.commit(types.SET_BACKGROUND, {
                    tmb: item.tmb,
                    type: item.type,
                    url: canvas.toDataURL(),
                    author: item.author,
                    colorState: colorHelper.getImageColorState(img)
                });
                this.loadingBackgroundPicture = null;
                this.loadingFromUrl = false;
                if(this.inputUrl){
                    this.inputUrl = '';
                }
                this.detectTheme();
            };
            img.onerror = () => {
                console.log('Cant load image');
                this.loadingBackgroundPicture = null;
                this.loadingFromUrl = false;
                // TODO
            }
            img.src = item.url;
        },
        loadBackgroundFromFile (type, e) {
            let file = new FileReader();
            file.addEventListener('load', () => {
                if(e.target.files[0].size < 10000000 && e.target.files[0].type.split('/')[0] === 'image'){
                    let newImg = new Image();
                    newImg.crossOrigin = 'Anonymous'; // https://developer.mozilla.org/en-US/docs/Web/HTML/CORS_enabled_image
                    newImg.onload = () => {
                        this.$store.commit(types.SET_BACKGROUND, {
                            type: type,
                            url: file.result,
                            colorState: colorHelper.getImageColorState(newImg)
                        });
                        e.target.value = '';
                        this.detectTheme();
                    };
                    newImg.src = file.result;
                }
            }, false);
            file.readAsDataURL(e.target.files[0]);
        },
        loadPicker (startColor, prop, e) {
            this.$root.$emit('show-color-picker', {
                left: e.pageX,
                top: e.pageY,
                startColor: startColor,
                prop: prop,
                changeFn (decorProp, value) {
                    this.$store.commit(types.SET_DECOR_PROP, {prop: decorProp, value: value});
                }
            });
        },
        resetColor (decorProp) {
            this.$store.commit(types.SET_DECOR_PROP, {prop: decorProp, value: []});
            if(decorProp === 'background.effect.color'){
                this.backgroundEffect = '';
            } // TODO -_-
        }
    }
}